.next-deliv {
  display: none;
}

@media (max-width: 1000px) {
  .next-deliv {
    position: absolute;
    z-index: 2;
    overflow-y: scroll;
    overflow-x: hidden;
    inset-inline-start: 0px;
    inset-block-start: 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    inline-size: 80vw;
    block-size: 100vh;
    margin-block-start: 0px;
    padding-block-start: 0px;
    background-color: var(--clr-white);
    background-color: white;
    transform: translateX(-80vw);
    transition: transform var(--tr);
    &__btn {
      align-self: start;
      display: flex;
      align-items: center;
      gap: 5px;
      margin-block-start: 15px;
      margin-block-end: 30px;
      font-size: 15px;
      font-weight: var(--fw-b);
      i {
        font-size: 28px;
        transform: rotate(180deg);
      }
    }
    a {
      flex-shrink: 0;
      overflow: hidden;
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 7px;
      aspect-ratio: 1 / 1;
      inline-size: 159px;
      block-size: auto;
      margin: 0 !important;
      padding-inline: 14px;
      padding-block: 20px;
      color: var(--clr-dark);
      background-color: var(--clr-white);
      background-repeat: no-repeat;
      background-position: center bottom;
      border: 1px solid var(--clr-dark);
      border-radius: 50%;
    }
    a.meat {
      background-image: url('../img/uc-deliveries__slider/meat.webp');
    }
    a.fish {
      background-image: url('../img/uc-deliveries__slider/fish.webp');
    }
    a.text {
      justify-content: space-between;
      time {
        margin-block-start: auto;
      }
    }
    p {
      font-size: 13px;
      font-weight: var(--fw-b);
      text-align: center;
    }
    .title {
      font-size: 16px;
    }
    time {
      font-family: var(--ff-sec);
      font-weight: var(--fw-sb);
    }
    a::before {
      content: '';
      display: block;
      position: absolute;
      z-index: 1;
      inset: 0;
      border: 1px solid transparent;
      border-radius: 100vw;
      pointer-events: none;
      transition: border-color var(--tr);
    }
    a:hover::before {
      border-color: var(--clr-dark)
    }
  }
  .next-deliv.active {
    transform: translateX(0vw);
  }
  .next-deliv::-webkit-scrollbar {
    width: 5px;
  }
  .next-deliv::-webkit-scrollbar-track {
    background-color: transparent;
    border-radius: 100vw;
  }
  .next-deliv::-webkit-scrollbar-thumb {
    background-color: hsl(0 0% 0% / .3);
    border-radius: 100vw;
  }
}
