.tabs-acc {
  // background-color: #FCF9C4;
  margin-block-end: 50px;
  border-radius: 10px;
  box-shadow: 0px 0px 20px 0px rgba(138, 133, 116, 0.38);
}

.tabs-acc__nav {
  position: relative;
  display: flex;
  justify-content: space-between;
  block-size: 40px;
  border: 1px solid #420E08;
  border-radius: 10px;
}

.tabs-acc__tab {
  --num: 4;
  position: relative;
  z-index: 1;
  inline-size: calc(100% / var(--num));
  background-color: transparent;
  font-size: 20px;
  font-weight: 700;
  border: none;
  cursor: pointer;
}

.tabs-acc__tab.active {
  color: white;
}

.tabs-acc__btn {
  display: none;
  justify-content: center;
  align-items: center;
  gap: 10px;
  inline-size: 100%;
  block-size: 45px;
  color: white;
  background-color: #995f36;
  background-image: url('../img/misc/wood-texture.webp');
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
  font-size: 18px;
  font-weight: 700;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  i {
    transform: rotate(90deg);
    transition: transform var(--tr);
  }
}

.tabs-acc__btn.active {
  color: white;
  i {
    transform: rotate(-90deg);
  }
}

.tabs-acc__btn-anim {
  position: absolute;
  z-index: 0;
  left: 0;
  top: 0;
  display: block;
  width: 300px;
  height: 100%;
  background-image: url('../img/misc/wood-texture.webp');
  background-repeat: no-repeat;
  background-size: 104% 116%;
  background-position: center;
  border-radius: 10px;
  transition: transform var(--tr);
}

.tabs-acc__content {
  overflow-y: auto;
  overflow-x: hidden;
  max-block-size: 400px;
  transition: max-block-size .5s ease-in-out;
}

.tabs-acc__content.hidden {
  max-block-size: 0;
}

.tabs-acc__content::before {
  content: '';
  display: block;
  position: sticky;
  z-index: 100;
  left: 0;
  top: 0;
  width: 100%;
  height: 20px;
  background-color: var(--clr-bg);
}

.tabs-acc__content::after {
  content: '';
  display: block;
  position: sticky;
  z-index: 100;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 20px;
  background-color: var(--clr-bg);
  border-radius: 0 0 10px 10px;
}

.tabs-acc__content-inner {
  padding: 0px 30px 0;
  font-size: 16px;
  p:not(:last-child) {
    margin-block-end: 15px;
  }
}

.tab-acc__testimonial:not(:last-child) {
  margin-block-end: 30px;
  padding-block-end: 30px;
  border-block-end: 1px solid var(--clr-dark);
}

.testimonial-top {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 10px;
  margin-block-end: 10px;
  .icon-img {
    font-size: 30px;
    cursor: pointer;
  }
}

.testimonial-name {
  flex-grow: 1;
  margin-block-end: 0px !important;
  font-size: 20px;
  font-weight: var(--fw-b);
}

.testimonial-text {
  margin-block-end: 20px;
}

.testimonial-response {}

.testimonial-response__title {
  margin-block-end: 10px;
  font-size: 20px;
  font-weight: var(--fw-b);
}

.q-title {
  margin-block-end: 5px !important;
  font-weight: var(--fw-b);
}

.q-answer {
  margin-block-end: 20px !important;
}

@media(max-width: 1200px) {
  .tabs-acc {
    background-color: unset;
    box-shadow: unset;
  }
  .tabs-acc__nav {
    display: none;
  }
  .tabs-acc__btn {
    position: relative;
    display: flex;
    margin-block-start: 15px;
  }
  .tabs-acc__btn.active::before {
    content: '';
    display: block;
    position: absolute;
    z-index: -1;
    inset: 0;
    background-color: var(--clr-yellow);
    border-radius: 10px 10px 0 0;
  }
  .tabs-acc__content {
    position: relative;
    z-index: 0;
    background-color: var(--clr-bg);
    border-radius: 0 0 10px 10px;
    box-shadow: 0px 0px 20px 0px rgba(138, 133, 116, 0.38);
  }
  .tabs-acc__content-inner {
    padding: 0 30px;
  }
}

@media (max-width: 600px) {
  .tabs-acc {
    &__content-inner {
      padding-inline: 15px;
    }
  }
}

.tabs-acc__content::-webkit-scrollbar {
  width: 3px;
}

.tabs-acc__content::-webkit-scrollbar-track {
  background-color: transparent;
  border-radius: 100vw;
}

.tabs-acc__content::-webkit-scrollbar-thumb {
  background-color: #420E08;
  border-radius: 100vw;
}

