*,
*::before,
*::after {
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd,
ul,
li,
button {
  margin: 0;
}

ul,
ol {
  padding: 0;
  list-style: none;
}

html {
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
}

a:not([class]) {
  color: var(--clr-dark);
}

img {
  width: 100%;
  display: block;
  object-fit: cover;
}

input,
button,
textarea,
select {
  font: inherit;
}

button {
  padding: 0;
  margin: 0;
  border: none;
  background-color: transparent;
  outline: none;
  cursor: pointer;
}

a {
  text-decoration: none;
}

button,
a,
label>input[type='checkbox'] {
  cursor: pointer;
}


/*-------  L I G H T B O X  ----------*/

.sl-overlay {
  background-color: var(--clr-black);
}

.sl-prev,
.sl-next {
  color: transparent !important;
}

