.crumbs {
  display: flex;
  flex-wrap: wrap;
  column-gap: 4px;
  row-gap: 6px;
  margin-block-end: 30px;
  font-size: 14px;
  color: var(--clr-sec);
  transition: color var(--tr);
  a {
    display: flex;
    align-items: center;
    gap: 4px;
    color: inherit;
  }
  a::after {
    content: "\e91e";
    font-family: icomoon;
    color: currentColor;
  }
  a:hover {
    color: var(--clr-dark);
  }
}
