.products-tabs {
  margin-block-end: 60px;
  .tabs__nav {
    --gap: 30;
    overflow: hidden;
    position: relative;
    gap: calc(var(--gap) * 1px);
    margin-block-end: 25px;
    block-size: 40px;
    border: 1px solid var(--clr-dark);
    border-radius: 10px;
  }
  .tabs__nav-btn {
    flex-basis: 25%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--clr-dark);
    background-color: transparent;
    font-size: clamp(15px, calc(6.72px + 0.83vw), 20px);
    font-weight: var(--fw-b);
    border-radius: 10px;
    transition: color var(--tr), background-size var(--tr);
  }
  .tabs__nav-btn>span {
    position: relative;
  }
  .tabs__nav-btn>span::before {
    content: '';
    position: absolute;
    inset-inline-start: 0;
    inset-block-end: 2px;
    inline-size: 0%;
    display: block;
    block-size: 1px;
    background-color: currentColor;
    transition: inline-size var(--tr);
  }
  .tabs__nav-btn:hover span::before {
    inline-size: 100%;
  }
  .tabs__nav-btn--active {
    color: #fff;
    // background-image: url('../img/misc/wood-texture.webp');
    background-repeat: no-repeat;
    background-size: 104% 116%;
    background-position: center;
    transition: color var(--tr);
  }
  .tabs__nav-btn-anim {
    position: absolute;
    z-index: 0;
    left: 0;
    top: 0;
    display: block;
    width: 300px;
    height: 100%;
    background-image: url('../img/misc/wood-texture.webp');
    background-repeat: no-repeat;
    background-size: 104% 116%;
    background-position: center;
    border-radius: 10px;
    transition: transform var(--tr);
  }
}

.product-desc-tabs {
  overflow: hidden;
  margin-block-end: 50px;
  padding-block-end: 30px;
  background-color: var(--clr-yellow);
  border-radius: 10px;
  box-shadow: 0px 0px 20px 0px rgba(138, 133, 116, 0.38);
}

.tabs__inner {
  overflow-y: auto;
  overflow-x: hidden;
  block-size: 270px;
  padding: 0 30px 25px;
}

.tabs__inner>p+p {
  margin-block-start: 10px;
}

.tabs__inner::-webkit-scrollbar {
  width: 5px;
}

.tabs__inner::-webkit-scrollbar-track {
  background-color: transparent;
  border-radius: 100vw;
}

.tabs__inner::-webkit-scrollbar-thumb {
  background-color: var(--clr-sec);
  border-radius: 100vw;
}

@media (max-width: 1120px) {
  .tabs__nav-btn span span {
    display: none;
  }
}

@media (max-width: 1000px) {
  .products-tabs {
    .tabs__nav-btn {
      font-size: 20px;
    }
    .tabs__nav-btn>span {
      line-height: .8;
    }
    .tabs__nav-btn>span::before {
      display: none;
    }
  }
}

@media (max-width: 700px) {
  .tabs__panel {
    .swiper-container {
      // inline-size: calc(100vw - 66px);
    }
  }
}

@media (max-width: 640px) {
  .products-tabs {
    .tabs__nav {
      justify-content: space-between;
      gap: 0;
    }
    .tabs__nav-btn {
      flex-basis: unset;
      padding-inline: 3vw;
      font-size: clamp(12px, calc(4px + 2.50vw), 20px);
    }
  }
}

@media (max-width: 480px) {
  .tabs__panel {
    .swiper-container {
      // inline-size: calc(100vw - 40px);
    }
  }
}
