.socials-block {
  .h2 {
    margin-block-end: 15px;
  }
  &__links {
    display: flex;
    flex-wrap: wrap;
    margin-block-end: 24px;
    column-gap: 15px;
    row-gap: 10px;
    a {
      display: flex;
      align-items: center;
      gap: 8px;
      padding: 4px 10px;
      border: 1px solid var(--clr-dark);
      border-radius: 100vw;
      transition: border-color var(--tr), color var(--tr);
      i {
        display: flex;
        justify-content: center;
        align-items: center;
        inline-size: 30px;
        block-size: 30px;
        background-color: var(--clr-light);
        font-size: 24px;
        border-radius: 100vw;
        transition: background-color var(--tr), color var(--tr);
      }
    }
    a:hover {
      color: var(--clr-dark-green);
      border-color: var(--clr-light-green);
      i {
        color: var(--clr-white);
        background-color: var(--clr-light-green);
      }
    }
  }
  &__photos {
    display: grid;
    grid-template-columns: repeat(9, 1fr);
    grid-template-rows: 1fr 1fr;
    gap: clamp(15px, calc(-10.08px + 2.50vw), 30px);
    .img {
      overflow: hidden;
      justify-self: stretch;
      align-self: stretch;
      block-size: 100%;
      border-radius: 20px;
      img {
        block-size: 100%;
        border-radius: 20px;
        transition: transform var(--tr);
      }
      img:hover {
        transform: scale(1.1);
      }
    }
    .sausage {
      grid-column: 1 / span 3;
      grid-row: 1 / span 1;
    }
    .fish {
      grid-column: 4 / span 2;
      grid-row: 1 / span 1;
    }
    .person {
      grid-column: 6 / span 4;
      grid-row: 1 / span 2;
    }
    .berries {
      grid-column: 1 / span 2;
      grid-row: 2 / span 1;
    }
    .dairy {
      grid-column: 3 / span 3;
      grid-row: 2 / span 1;
    }
  }
}

@media (max-width: 1000px) {
  .socials-block {
    .h2 {
      text-align: center;
    }
    &__photos {
      gap: clamp(15px, calc(1.12px + 2.88vw), 30px);
    }
  }
}

@media (max-width: 480px) {
  .socials-block {
    &__photos {
      grid-template-columns: 1fr 1fr;
      grid-template-rows: 170px 370px 170px;
      .sausage {
        grid-column: 1 / span 1;
        grid-row: 1 / span 1;
      }
      .fish {
        grid-column: 2 / span 1;
        grid-row: 1 / span 1;
      }
      .person {
        grid-column: 1 / span 2;
        grid-row: 2 / span 1;
      }
      .berries {
        grid-column: 1 / span 1;
        grid-row: 3 / span 1;
      }
      .dairy {
        grid-column: 2 / span 1;
        grid-row: 3 / span 1;
      }
    }
  }
}
