.icon-categ {
  display: block;
  aspect-ratio: 1 / 1;
  inline-size: 44px;
  block-size: auto;
  border: 1px solid var(--clr-dark);
  border-radius: 100vw;
  background-color: var(--clr-light);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100%;
  transition: inline-size var(--tr), background-size var(--tr);
}

.icon-categ--bus {
  background-image: url('../img/i-pics/bus.webp');
}

