.delivery-block {
  min-block-size: 350px;
  margin-block-end: clamp(25px, calc(21.28px + 1.17vw), 40px);
}

@media (max-width: 600px) {
  .delivery-block.auto-grid {
    grid-template-columns: 1fr 1fr;
    gap: 15px;
    min-block-size: 275px;
    .product-card__order {
      flex-direction: column;
      gap: 6px;
    }
    .product-card__order-btn {
      order: 1;
    }
    .product-card__count {
      order: 0;
    }
  }
}


/*------------------------------------------------------------*/

.delivery-block.list {
  min-block-size: 175px;
  margin-block-end: clamp(25px, calc(21.28px + 1.17vw), 40px);
}

@media (min-width: 1001px) {
  .delivery-block.list {
    display: block;
    .product-card {
      display: grid;
      grid-template-columns: max-content 1fr;
      column-gap: 30px;
      margin-block-end: 15px;
      padding: 25px 0 25px 25px;
      &__top {
        align-self: start;
        inline-size: 225px;
        block-size: 150px;
        border: 1px solid var(--clr-light-green);
        border-radius: 20px;
        img {
          border-radius: 20px;
        }
      }
      &__btm {
        display: grid;
        grid-template-columns: 1fr;
        align-items: start;
        padding: 0 40px 0 0;
      }
      &__title {
        grid-column: 1 / span 1;
        grid-row: 1 / span 1;
        margin-block-end: 10px;
      }
      &__price {
        justify-self: start;
        grid-column: 1 / span 1;
        grid-row: 2 / span 1;
        justify-content: end;
        margin-block-start: unset;
        margin-block-end: 20px;
      }
      &__price :nth-child(1) {
        order: 0;
      }
      &__price :nth-child(2) {
        order: 1;
      }
      &__order {
        justify-self: start;
        grid-column: 1 / span 1;
        grid-row: 3 / span 1;
        margin-block: 0 15px;
      }
      &__nutr-value {
        grid-column: 1 / span 1;
        grid-row: 4 / span 1;
        display: block;
      }
      &__package-weight {
        grid-column: 1 / span 1;
        grid-row: 5 / span 1;
        display: block;
        margin-block-start: 15px;
      }
      &__package {
        display: none;
      }
      &__extra {
        inset-block-end: 0px;
      }
    }
  }
}

@media (min-width: 1280px) {
  .delivery-block.list {
    .product-card {
      &__btm {
        grid-template-columns: 1fr max-content;
        align-content: start;
        column-gap: 20px;
      }
      &__price {
        grid-column: 2 / span 1;
        grid-row: 1 / span 1;
        justify-self: end;
        margin-block-start: unset;
        margin-block-end: 15px;
      }
      &__price :nth-child(1) {
        order: 1;
      }
      &__price :nth-child(2) {
        order: 0;
      }
      &__order {
        grid-column: 2 / span 1;
        grid-row: 2/ span 1;
        margin-block: 0 15px;
      }
      &__nutr-value {
        grid-column: 1 / span 1;
        grid-row: 2 / span 1;
        max-inline-size: 500px;
      }
      &__package-weight {
        grid-column: 1 / span 1;
        grid-row: 3 / span 1;
        margin-block-start: 15px;
      }
    }
  }
}

@media (max-width: 600px) {
  .delivery-block.list.auto-grid {
    grid-template-columns: 1fr 1fr;
    gap: 15px;
    min-block-size: 275px;
    .product-card__order {
      flex-direction: column;
      gap: 6px;
    }
    .product-card__order-btn {
      order: 1;
    }
    .product-card__count {
      order: 0;
    }
  }
}
