.swiper-container {
  --arr-w: 19px;
  position: relative;
  .swiper-controls {
    position: absolute;
    z-index: 1;
    left: 0;
    top: 50%;
    display: flex;
    justify-content: space-between;
    width: calc(100% + var(--arr-w) * 2);
    transform: translateX(calc(0px - var(--arr-w)));
    pointer-events: none;
  }
  .swiper-button-prev,
  .swiper-button-next {
    position: static;
    background-repeat: no-repeat;
    aspect-ratio: 0.5428 / 1;
    inline-size: var(--arr-w);
    block-size: auto;
    pointer-events: auto;
  }
  .swiper-button-next {
    background-image: url('../img/svg/icons/arrow-next.svg');
  }
  .swiper-button-prev {
    background-image: url('../img/svg/icons/arrow-prev.svg');
  }
  .swiper-button-next.swiper-button-disabled,
  .swiper-button-prev.swiper-button-disabled {
    opacity: .0;
  }
  .swiper-button-next::after,
  .swiper-button-prev::after {
    content: '';
  }
}

.swiper {
  width: 100%;
  &-wrapper {}
  &-slide {
    height: unset;
  }
  .product-card {
    margin-inline: 4px;
  }
}

@media (max-width: 860px) {
  .swiper-container {
    --arr-w: 13px;
  }
}
