.farmers {
  --m-p-extra: 15px;
  margin-block-end: 50px;
  &__title {
    display: inline-block;
    margin-block-end: 10px;
    color: var(--clr-dark);
    text-decoration: underline;
  }
  &__title:hover {
    text-decoration: none;
  }
  .swiper-container {
    margin-inline: calc(0px - var(--m-p-extra));
    margin-block-end: 10px;
    .swiper-controls {
      width: calc(100% + var(--arr-w) * 2 - var(--m-p-extra) * 2);
      transform: translateX( calc(0px - var(--arr-w) + var(--m-p-extra)));
    }
  }
  .swiper {
    padding: var(--m-p-extra);
  }
  &__contacts {
    display: flex;
    align-items: center;
    gap: 40px;
    max-inline-size: 908px;
    padding: 20px 35px;
    border: 1px solid var(--clr-dark);
    border-radius: 100vw;
    h3 {
      flex-shrink: 0;
      inline-size: max-content;
    }
  }
  &__form {
    display: flex;
    align-items: center;
    gap: 5px;
    inline-size: 100%;
    block-size: 100%;
  }
  &__form-item {
    align-self: stretch;
    flex-grow: 1;
  }
  &__form-label {
    display: none;
  }
  &__form-input {
    inline-size: 100%;
    block-size: 100%;
    padding-inline: 20px 30px;
    font-family: var(--ff-sec);
    color: var(--clr-sec);
    font-size: 14px;
    font-weight: var(--fw-m);
    border: none;
    border-radius: 100vw;
    box-shadow: 0px 0px 20px 0px rgba(138, 133, 116, 0.38);
  }
  &__form-input:focus {
    border: 2px solid var(--clr-light-green);
    outline: none;
  }
  &__form-btn {
    padding-inline: 56px;
  }
  &__form-error {
    transform: translateX(20px);
  }
}

.farmers__slide {
  overflow: hidden;
  position: relative;
  // display: flex;
  // flex-wrap: wrap;
  // column-gap: 25px;
  // row-gap: 15px;
  padding: 20px 20px 0 0;
  //////////////////////////
  display: grid;
  grid-template-columns: 1fr 168px;
  grid-template-rows: auto min-content;
  column-gap: 25px;
  block-size: 100%;
  //////////////////////////
  color: var(--clr-dark);
  background-color: var(--clr-bg);
  border-radius: 26px;
  box-shadow: 0px 0px 15px 0px rgba(138, 133, 116, 0.38);
  .img {
    grid-column: 2 / span 1;
    grid-row: 1 / span 2;
    // order: 1;
    overflow: hidden;
    inline-size: 158px;
    margin-block-end: 20px;
    block-size: 168px;
    border-radius: 26px;
    img {
      block-size: 100%;
      border-radius: 26px;
    }
  }
  .desc {
    grid-column: 1 / span 1;
    grid-row: 1 / span 1;
    // order: 0;
    // inline-size: calc(100% - 190px);
    display: flex;
    flex-direction: column;
    block-size: 100%;
  }
  .title {
    margin-block-end: 13px;
    padding-inline-start: 20px;
    font-size: 20px;
    font-weight: var(--fw-b);
  }
  .text {
    margin-block-end: 13px;
    padding-inline-start: 20px;
    font-size: 14px;
  }
  .misc {
    align-self: end;
    grid-column: 1 / span 2;
    grid-row: 2 / span 1;
    // order: 2;
    inline-size: 100%;
    block-size: 38px;
    display: flex;
    align-items: center;
    column-gap: 30px;
    // transform: translateY(-53px);
    .prod-num {
      align-self: end;
      display: flex;
      justify-content: center;
      align-items: center;
      min-inline-size: 104px;
      block-size: 38px;
      padding: 10px;
      color: var(--clr-white);
      background-color: var(--clr-light-green);
      font-size: 14px;
      border-radius: 0 26px 0 0;
      transition: background-color var(--tr);
    }
    // a:hover {
    //   background-color: var(--clr-dark-green);
    // }
  }
  .icons {
    display: flex;
    flex-wrap: wrap;
    gap: 2px;
    pointer-events: none;
  }
  .icon-categ {
    inline-size: 23px;
    background-color: var(--clr-white);
  }
}

.farmers__slide::before {
  content: '';
  display: block;
  position: absolute;
  inset: 0;
  border: 2px solid transparent;
  border-radius: 26px;
  transition: border-color var(--tr);
}

.farmers__slide:hover::before {
  border-color: var(--clr-light-green);
}

@media (max-width: 1280px) {
  .farmers {
    &__contacts {
      flex-wrap: wrap;
      gap: 15px;
      padding-inline: 20px;
      border-radius: 26px;
      h3 {
        inline-size: 100%;
      }
    }
  }
}

@media (max-width: 1000px) {
  .farmers {
    &__title {
      text-align: center;
    }
    .swiper-container {
      justify-self: center;
    }
    &__contacts {
      max-inline-size: 700px;
      h3 {
        text-align: center;
      }
    }
  }
}

@media (max-width: 520px) {
  .farmers {
    .swiper {
      .swiper-slide {
        height: 100%;
      }
    }
    &__contacts {}
    &__form {
      flex-direction: column;
      gap: 25px;
    }
    &__form-item {
      inline-size: 100%;
      block-size: 40px;
    }
    &__form-btn {
      inline-size: 100%;
    }
  }
  .farmers__slide {
    display: block;
    flex-wrap: nowrap;
    flex-direction: column;
    padding: 15px;
    .img {
      order: 0;
      aspect-ratio: 1/1;
      inline-size: 100%;
      block-size: 100%;
      margin-block-end: 15px;
    }
    .title {
      padding-inline: 0;
      margin-block-end: 25px;
    }
    .desc {
      order: 1;
      display: block;
      inline-size: 100%;
      margin-block-end: 15px;
    }
    .text {
      padding-inline: 0;
    }
    .misc {
      transform: unset;
      margin-inline-start: -15px;
      margin-block-end: -15px;
    }
  }
}

@media (max-width: 480px) {
  .farmers__slide {
    .icons {
      padding-block: 10px;
    }
  }
}
