.content {
  grid-area: content;
  position: relative;
  z-index: 1;
  margin-block-end: 60px;
  padding-inline: clamp(40px, calc(-40px + 8.00vw), 88px) var(--pad-right);
  &__inner {
    position: relative;
    inline-size: 100%;
  }
}

.content.content--pt {
  padding-block-start: 130px;
}

.aside {
  position: relative;
  z-index: 3;
  grid-area: aside;
  margin-block-end: 60px;
  padding-inline-start: 16px;
}

.aside.aside--pt {
  padding-block-start: 130px;
}

@media (max-width: 1000px) {
  .content {
    padding-inline: var(--pad-right);
    &__inner {
      padding-block-start: 50px;
    }
  }
  .aside {
    overflow-y: scroll;
    overflow-x: hidden;
    position: absolute;
    z-index: 3;
    inset-inline-start: 0;
    inset-block-start: 0;
    inline-size: unset;
    background-color: var(--clr-white);
    inline-size: 80vw;
    block-size: calc(100vh - 60px);
    margin-block-start: 60px;
    margin-block-end: unset;
    transform: translateX(-80vw);
    transition: transform var(--tr);
  }
  .aside.active {
    transform: translateX(0vw);
  }
  .aside::-webkit-scrollbar {
    width: 5px;
  }
  .aside::-webkit-scrollbar-track {
    background-color: transparent;
    border-radius: 100vw;
  }
  .aside::-webkit-scrollbar-thumb {
    background-color: hsl(0 0% 0% / .3);
    border-radius: 100vw;
  }
}

@media (max-width: 680px) {
  .content {
    padding-inline: var(--pad-right);
  }
}
