.guarantee {
  display: grid;
  grid-template-columns: max-content 1fr max-content;
  align-items: center;
  column-gap: 40px;
  margin-block-end: 50px;
  padding: 10px 15px;
  background-color: var(--clr-bg-yellow);
  border-radius: 20px;
  box-shadow: 0px 0px 20px 0px rgba(64, 47, 30, 0.12);
  &__video {
    overflow: hidden;
    inline-size: 249px;
    block-size: 183px;
    // background-color: var(--clr-white);
    border-radius: 20px;
  }
  &__desc {}
  &__text {
    margin-block-end: 36px;
    font-size: 15px;
    font-style: italic;
    line-height: 1.4;
  }
  &__name {
    font-size: 20px;
    font-weight: var(--fw-b);
  }
  &__img {
    inline-size: 156px;
    block-size: 151px;
  }
}

@media (max-width: 1320px) {
  .guarantee {
    position: relative;
    grid-template-columns: max-content 1fr;
    align-items: start;
    padding-block-end: 25px;
    &__img {
      position: absolute;
      inset-inline-end: 0;
      inset-block-end: -20%;
      transform: scale(.9);
    }
  }
}

@media (max-width: 1100px) {
  .guarantee {
    padding-block-end: 15px;
    &__img {
      display: none;
    }
  }
}

@media (max-width: 1000px) {
  .guarantee {
    &__img {
      display: block;
    }
  }
}

@media (max-width: 800px) {
  .guarantee {
    &__img {
      display: none;
    }
  }
}

@media (max-width: 600px) {
  .guarantee {
    grid-template-columns: 1fr;
    padding-block-end: 50px;
    &__video {
      aspect-ratio: 1.77777 / 1;
      inline-size: 100%;
      block-size: auto;
      margin-block-end: 20px;
    }
    &__img {
      display: block;
      inset-block-end: -8%;
      transform: scale(.8);
    }
  }
}
