.popup.pop-order-wrap {
  max-inline-size: 844px;
  inline-size: 100%;
}

.pop-order-wrap {
  .pop-order {
    background-color: var(--clr-bg);
    display: block;
    padding: 35px 65px;
    border-radius: 20px;
    outline: 1px solid var(--clr-dark);
    outline-offset: -5px;
  }
  .order {
    display: block;
    margin-block-end: unset;
    &__title-block {
      margin-block-end: 25px;
    }
    &__title {
      color: var(--clr-dark);
      font-size: 30px;
      text-transform: uppercase;
      i {
        display: none;
      }
    }
    &__title-block-btns {
      display: none;
    }
    &__products-items {
      overflow-y: auto;
      overflow-x: hidden;
      max-block-size: 400px;
      padding-inline-end: 5px;
    }
    &__products-items::-webkit-scrollbar {
      width: 3px;
    }
    &__products-items::-webkit-scrollbar-track {
      background-color: #C7BFA7;
      border-radius: 100vw;
    }
    &__products-items::-webkit-scrollbar-thumb {
      background-color: var(--clr-dark);
      border-radius: 100vw;
    }
    &__item {
      padding: 0;
      background-color: transparent;
      box-shadow: none;
    }
    &__products-item {
      grid-template-columns: 1fr 70px 80px 90px;
      .pr-desc {
        margin-inline-end: 20px;
        padding-block: 12px;
        cursor: auto;
        i {
          display: none;
        }
      }
      .pr-title {
        max-inline-size: 180px;
        color: var(--clr-dark);
        cursor: auto;
      }
      .pr-count {
        background-color: transparent;
        input {
          font-weight: var(--fw-b);
        }
      }
      .icon-minus,
      .icon-plus {
        display: none;
      }
    }
    .pr-old {
      display: none;
    }
    &__overall-sum-wrap {
      display: flex;
      justify-content: space-between;
      margin-block-end: 40px;
      padding-block-start: 15px;
      color: var(--clr-dark);
      font-size: 20px;
      font-weight: var(--fw-b);
      border-block-start: 1px solid #C7C0A7;
    }
    &__overall-sum-title {}
    &__overall-sum {}
    &__form-quick-order {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 15px;
      margin-block-end: 15px;
      label {
        // display: none;
      }
      .form-group {
        inline-size: 100%;
      }
      .input,
      .comments {
        inline-size: 100%;
        padding: 7px 0 4px;
        background-color: transparent;
        font-family: var(--ff-sec);
        font-size: 16px;
        font-weight: var(--fw-m);
        line-height: 1.4;
        border: none;
        border-block-end: 1px solid #C7C0A7;
        outline: none;
      }
      .input:focus-visible,
      .comments:focus-visible {
        // outline: 1px solid var(--clr-dark);
        // outline-offset: -2px;
      }
      .name {
        grid-column: 1 / span 1;
        grid-row: 1 / span 1;
      }
      .email {
        grid-column: 1 / span 1;
        grid-row: 2 / span 1;
      }
      .tel {
        grid-column: 1 / span 1;
        grid-row: 3 / span 1;
      }
      .error {
        padding-block-start: 0;
      }
      .comments-wrap {
        grid-column: 2 / span 1;
        grid-row: 1 / span 2;
        display: grid;
        grid-template-rows: min-content 1fr;
      }
      .btn {
        grid-column: 2 / span 1;
        grid-row: 3 / span 1;
        align-self: end;
        block-size: 45px;
        font-size: 20px;
      }
    }
    .agreement {
      color: var(--clr-sec);
      font-size: 14px;
    }
  }
}

@media (max-width: 700px) {
  .pop-order-wrap {
    .pop-order {
      padding: 30px 30px;
    }
    .order {
      &__products-item {
        grid-template-columns: 1fr 1fr 1fr;
        .pr-title {
          max-inline-size: unset;
        }
        .pr-count {
          padding-block: 0;
          input {
            font-size: 17px;
          }
        }
      }
      &__form-quick-order {
        gap: 10px;
        .input,
        .comments {
          padding: 10px 20px;
          font-size: 15px;
        }
        .btn {
          font-size: 15px;
        }
      }
    }
  }
}

@media (max-width: 600px) {
  .pop-order-wrap {
    .pop-order {
      padding: 20px 20px;
    }
    .order__title-block {
      justify-content: center;
      margin-block-end: 15px;
    }
    .order__title {
      justify-self: center;
      font-size: 21px;
    }
    .order {
      &__products-item {
        padding-block-end: 12px;
      }
      &__overall-sum-wrap {
        margin-block-end: 30px;
      }
      &__form-quick-order {
        grid-template-columns: 1fr;
        column-gap: 0;
        margin-block-end: 10px;
        .input,
        .comments {
          padding: 4px 0 3px;
        }
        .name {
          grid-column: 1 / span 1;
          grid-row: 1 / span 1;
        }
        .email {
          grid-column: 1 / span 1;
          grid-row: 2 / span 1;
        }
        .tel {
          grid-column: 1 / span 1;
          grid-row: 3 / span 1;
        }
        .comments-wrap {
          grid-column: 1 / span 1;
          grid-row: 4 / span 1;
        }
        .btn {
          grid-column: 1 / span 1;
          grid-row: 5 / span 1;
        }
      }
      .agreement {
        text-align: center;
      }
    }
  }
}
