:root {
  --max-container: 1600px;
  --pad-right: 35px;
  --pad-right: clamp(0.75rem, calc(-0.06rem + 3.59vw), 2.19rem);
  --ff-prim: "PTSans", system-ui, sans-serif;
  --ff-sec: "Montserrat", system-ui, sans-serif;
  --fz-base: 16px;
  --fz-s: 14px;
  --fw-r: 400;
  --fw-m: 500;
  --fw-sb: 600;
  --fw-b: 700;
  --clr-light: #F5EFDA;
  --clr-dark: #420E08;
  --clr-dark-hover: #240603;
  --clr-brown: #B16F33;
  --clr-sec: #7E7968;
  --clr-yellow: #FCF9C4;
  --clr-bg-yellow: #ECD89B;
  --clr-bg: #EEE4C2;
  --clr-dark-green: #6B9722;
  --clr-light-green: #7EAC30;
  --clr-price: #7B1E00;
  --clr-white: #fff;
  --clr-black: #000;
  --clr-red: #D31600;
  --clr-orange: #F88317;
  --tr: .3s ease-in-out;
}
