@font-face {
  font-family: 'icomoon';
  src: url('../fonts/icomoon.woff?p05b6q') format('woff'), url('../fonts/icomoon.svg?p05b6q#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

h1,
.h1,
h2,
.h2,
h3,
.h3 {
  font-weight: var(--fw-b);
  text-transform: uppercase;
}

h1 {
  font-size: 40px;
  line-height: 1.25;
}

h2,
.h2 {
  font-size: clamp(18px, calc(16px + 0.63vw), 26px);
  line-height: 1.46;
}

h3 {
  font-size: 20;
  line-height: 1.3;
}

[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

