body {
  display: grid;
  grid-template-areas: 'header header header header' '. aside content .' 'footer footer footer footer';
  grid-template-columns: 1fr 338px minmax(0px, 1294px) 1fr;
  grid-template-rows: max-content 1fr max-content;
  font-family: var(--ff-prim);
  font-size: var(--fz-base);
  line-height: var(--lh-base);
  color: var(--clr-dark);
  background-color: var(--clr-bg);
}

@media (max-width: 1000px) {
  body {
    grid-template-areas: 'header' 'content' 'aside' 'footer';
    grid-template-columns: 100%;
  }
}

.home {
  background: var(--clr-bg) no-repeat url('../img/misc/content__main-bg.webp') right top;
  h1 {
    font-size: clamp(21px, calc(15.52px + 1.53vw), 40px);
  }
}
