.page-title-block {
  display: grid;
  grid-template-columns: 1fr min-content;
  column-gap: 30px;
  margin-block-end: 30px;
  .stars {
    align-self: end;
    display: flex;
    gap: 0px;
    transform: translateY(-3px);
    i {
      color: var(--clr-orange);
      font-size: 27px;
    }
  }
}

@media (max-width: 600px) {
  .page-title-block {
    grid-template-columns: 1fr;
    column-gap: unset;
    row-gap: 5px;
    h1 {
      grid-row: 2 / span 1;
    }
    .stars {
      grid-row: 1 / span 1;
      i {
        font-size: 20px;
      }
    }
  }
}
