.testim {
  max-block-size: 1175px;
  margin-block-end: 50px;
  a {
    text-decoration: underline;
  }
  a:hover {
    text-decoration: none;
  }
  h2 {
    margin-block-end: 15px;
  }
}

.testim__slide {
  display: flex;
  flex-direction: column;
  row-gap: 20px;
  height: 100%;
  margin-inline: 2px;
  padding: 20px 25px;
  background-color: var(--clr-white);
  border: 2px solid var(--clr-light-green);
  border-radius: 20px;
  transition: border-color var(--tr);
  &-top {
    display: flex;
    align-items: center;
    gap: 10px;
  }
  &-name {
    flex-grow: 1;
    font-size: 24px;
    font-weight: var(--fw-b);
    color: var(--clr-light-green);
  }
  i {
    color: var(--clr-dark);
    font-size: 26px;
    cursor: pointer;
  }
  time {
    font-family: var(--ff-sec);
    font-size: 13px;
    font-weight: var(--fw-m);
    opacity: .5;
  }
  &-mid {}
  &-text {
    font-size: 14px;
  }
  &-btm {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-inline-start: 20px;
    padding-inline-start: 20px;
    border-inline-start: 2px solid var(--clr-sec);
  }
  &-subtitle {
    color: var(--clr-light-green);
    font-family: var(--ff-sec);
    font-size: 14px;
    font-weight: var(--fw-m);
  }
  &-response {
    color: var(--clr-light-green);
    font-size: 14px;
    font-style: italic;
  }
}

.testim__slide:hover {
  border-color: var(--clr-dark);
}

@media (max-width: 1000px) {
  .testim {
    h2 {
      text-align: center;
    }
  }
}

@media (max-width: 480px) {
  .testim__slide {
    &-name {
      font-family: var(--ff-sec);
      font-size: 18px;
      font-weight: var(--fw-sb);
    }
    &-btm {
      margin-inline-start: unset;
      padding-inline-start: 14px;
    }
  }
}
