.catalog {
  display: flex;
  flex-direction: column;
  align-items: center;
  inline-size: 322px;
  margin-block-start: 90px;
  &__search {
    display: none;
  }
  &__nav {
    display: flex;
    flex-direction: column;
    // gap: 6px;
    inline-size: 100%;
  }
  &__item {
    position: relative;
    display: flex;
    align-items: center;
    block-size: 50px;
  }
  &__item:nth-child(11) {
    margin-block: 7px;
  }
  &__item:hover {
    .catalog__item-text {
      padding-left: 45px;
    }
    .icon-categ {
      inline-size: 50px;
    }
  }
  &__item.has-submenu::before {
    content: '\e91b';
    position: absolute;
    inset-inline-end: 10px;
    inset-block-start: 50%;
    transform: translateY(-50%);
    font-family: icomoon;
    color: var(--clr-white);
    font-size: 24px;
    pointer-events: none;
  }
  &__item.has-submenu:hover {
    .catalog__item-submenu {
      visibility: visible;
      opacity: 1;
      transform: translateX(0px);
    }
  }
  & .icon-categ {
    position: relative;
  }
  &__item-text {
    overflow: hidden;
    flex-grow: 1;
    display: flex;
    align-items: center;
    margin-inline-start: -22px;
    padding-inline-start: 42px;
    padding-block: 13px;
    color: var(--clr-white);
    background-image: url('../img/misc/wood-texture.webp');
    background-repeat: no-repeat;
    background-size: 100% 100%;
    font-size: 17px;
    font-weight: var(--fw-b);
    line-height: 1;
    border-radius: 7px;
    transition: color var(--tr), padding-inline-start var(--tr);
  }
  &__item-submenu {
    position: absolute;
    inset-inline-start: 100%;
    inset-block-start: 0%;
    transform: translateX(-25px);
    visibility: hidden;
    opacity: 0;
    transition: opacity var(--tr), transform var(--tr);
  }
  &__submenu-inner {
    display: flex;
    flex-direction: column;
    row-gap: 15px;
    margin-inline-start: 15px;
    padding: 30px 45px;
    background-color: var(--clr-bg);
    border-radius: 15px;
    box-shadow: 0px 0px 20px 0px rgba(138, 133, 116, 0.38);
    outline: 1px solid var(--clr-dark);
    outline-offset: -10px;
    a {
      inline-size: max-content;
      color: var(--clr-dark);
      font-size: 17px;
    }
    a:hover {
      span::before {
        inline-size: 100%;
      }
    }
    a.hidden {
      display: none;
    }
    span {
      position: relative;
      display: inline-block;
    }
    span::before {
      content: '';
      position: absolute;
      inset-inline-start: 0;
      inset-block-end: 0;
      display: block;
      inline-size: 0%;
      block-size: 1px;
      background-color: var(--clr-dark);
      transition: inline-size var(--tr);
    }
  }
  &__submenu-inner.postavki {
    a span {
      inline-size: 130px;
    }
  }
}

@media (max-width: 1000px) {
  .catalog {
    row-gap: 30px;
    inline-size: unset;
    margin-block-start: unset;
    &__search {
      align-self: start;
      display: flex;
      inline-size: 250px;
    }
    &__search-form {
      inline-size: 100%;
    }
    &__search-input {
      position: relative;
      display: flex;
      align-items: center;
      inline-size: 100%;
      block-size: 30px;
      padding-inline: 36px 10px;
      border: 1px solid #C7C7C7;
      border-radius: 100vw;
      background-image: url("data:image/svg+xml,%3Csvg version='1.1' id='Layer_1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' x='0px' y='0px' viewBox='0 0 20 20' style='enable-background:new 0 0 20 20;' xml:space='preserve'%3E%3Cstyle type='text/css'%3E .st0%7Bfill-rule:evenodd;clip-rule:evenodd;fill:%23C7C7C7;%7D%0A%3C/style%3E%3Cg%3E%3Cpath class='st0' d='M10.7,2.2c-3.9,0-7.1,3.2-7.1,7.1c0,1.8,0.6,3.4,1.7,4.6l-3,3c-0.2,0.2-0.2,0.6,0,0.8c0.2,0.2,0.6,0.2,0.8,0 l3-3c1.2,1.1,2.8,1.7,4.6,1.7c3.9,0,7.1-3.2,7.1-7.1S14.6,2.2,10.7,2.2z M10.7,15.3c-3.3,0-6-2.7-6-6s2.7-6,6-6s6,2.7,6,6 S14,15.3,10.7,15.3z'/%3E%3Cpath class='st0' d='M10.7,5.3c-1,0-1.9,0.4-2.6,1.1C7.9,6.6,7.9,7,8.1,7.2c0.2,0.2,0.6,0.2,0.8,0c0.5-0.5,1.1-0.7,1.8-0.7 s1.3,0.3,1.8,0.7c0.2,0.2,0.6,0.2,0.8,0s0.2-0.6,0-0.8C12.6,5.7,11.7,5.3,10.7,5.3z'/%3E%3C/g%3E%3C/svg%3E");
      background-repeat: no-repeat;
      background-size: 20px;
      background-position: 10px center;
      font-size: 10px;
      line-height: 1;
    }
    &__search-input::placeholder {
      font-size: 10px;
      line-height: 1;
      color: #C7C7C7;
    }
    &__search-input:focus-visible {
      outline: none;
    }
    &__search-submit {
      display: none;
    }
    &__nav {
      gap: 15px;
    }
    &__item {
      position: relative;
      display: grid;
      grid-template-areas: 'pic text';
      grid-template-columns: 28px 1fr;
      align-items: start;
      column-gap: 15px;
      inline-size: 250px;
      block-size: unset;
    }
    &__item:hover {
      .catalog__item-text {
        color: unset;
      }
      .catalog__item-text {
        padding-left: unset;
      }
      .icon-categ {
        inline-size: unset;
      }
    }
    &__item.has-submenu {
      grid-template-areas: 'pic text' '. nav';
      .catalog__item-submenu {
        visibility: unset;
        opacity: unset;
        transform: unset;
        max-block-size: 0px;
        opacity: 1;
      }
    }
    &__item.has-submenu:hover {
      .catalog__item-submenu {
        visibility: unset;
        opacity: unset;
        transform: unset;
        max-block-size: 0px;
        opacity: 1;
      }
    }
    &__item.has-submenu::before {
      inset-inline-end: 10px;
      inset-block-start: 7px;
      transform: unset;
      color: lightgrey;
      font-size: 16px;
      opacity: unset;
      transition: transform var(--tr);
    }
    &__item.has-submenu.rotate::before {
      transform: rotate(90deg);
    }
    & .icon-categ {
      position: relative;
      grid-area: pic;
      inline-size: 28px;
      block-size: 28px;
      background-color: var(--clr-white);
    }
    &__item-text {
      grid-area: text;
      margin-inline-start: unset;
      padding-inline-start: unset;
      padding-block: unset;
      block-size: 28px;
      color: var(--clr-dark);
      background-image: none;
      font-size: 15px;
      line-height: 1;
      border-radius: unset;
      transition: unset;
    }
    &__item-submenu {
      overflow: hidden;
      grid-area: nav;
      position: static;
      inset-inline-start: unset;
      inset-block-start: unset;
      transform: unset;
      visibility: visible;
      padding-block-start: 0px;
      opacity: 0;
      max-block-size: 0;
      transition: max-block-size .6s ease-in-out, opacity .6s ease-in-out, padding-block-start .6s ease-in-out;
    }
    &__item-submenu.active {
      max-block-size: 400px !important;
    }
    &__submenu-inner {
      gap: 0;
      margin-inline-start: unset;
      padding: unset;
      background-color: var(--clr-white);
      border-radius: 15px;
      box-shadow: unset;
      outline: unset;
      a {
        padding-block-start: 10px;
        font-size: 15px;
      }
    }
    &__submenu-inner.postavki {
      a span {
        inline-size: 120px;
      }
    }
  }
}
