.prod-order-block {
  display: grid;
  grid-template-columns: 1fr min-content;
  margin-block-end: 25px;
  &__title {
    grid-column: 1 / span 1;
    grid-row: 1 / span 1;
    margin-block-end: 10px;
    color: var(--clr-dark-green);
    font-size: 17px;
    font-weight: var(--fw-b);
    line-height: 1.4;
  }
  &__list {
    grid-column: 1 / span 1;
    grid-row: 2 / span 1;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    column-gap: 20px;
    row-gap: 10px;
  }
  &__item {
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 17px;
    cursor: pointer;
    transition: color var(--tr);
    .icon-categ {
      inline-size: 36px;
      background-color: transparent;
      transition: border-color var(--tr);
    }
  }
  &__item:hover {
    color: var(--clr-dark-green);
    .icon-categ {
      border-color: var(--clr-dark-green);
    }
  }
  &__item.active {
    gap: 5px;
    padding-inline-end: 15px;
    color: var(--clr-white);
    background-image: url('../img/misc/wood-texture.webp');
    background-repeat: no-repeat;
    background-size: 90% 90%;
    background-position: right center;
    border-radius: 0 15px 15px 0;
    .icon-categ {
      background-color: var(--clr-light);
      border-color: var(--clr-dark-green);
    }
  }
  &__btns {
    grid-column: 2 / span 1;
    grid-row: 1 / span 2;
    align-self: end;
    display: flex;
    gap: 18px;
    cursor: pointer;
    transform: translateY(5px);
    button {
      color: var(--clr-dark);
      transition: color var(--tr), transform var(--tr)
    }
    i {
      font-size: 45px;
    }
    button:hover {
      color: var(--clr-dark-hover);
      transform: scale(1.05);
    }
    button.active {
      color: var(--clr-light-green);
      transition: color var(--tr), transform var(--tr)
    }
    button.active:hover {
      color: var(--clr-dark-green);
      transform: scale(1.05);
    }
  }
}

@media (max-width: 680px) {
  .prod-order-block {
    column-gap: 20px;
    row-gap: 10px;
    &__title {
      grid-column: 1 / span 1;
      grid-row: 1 / span 1;
      margin-block-end: unset;
      font-size: 14px;
    }
    &__list {
      grid-column: 1 / span 2;
      grid-row: 2 / span 1;
      column-gap: 15px;
    }
    &__item {
      gap: 5px;
      font-size: 14px;
      .icon-categ {
        inline-size: 30px;
      }
    }
    &__btns {
      grid-column: 2 / span 1;
      grid-row: 1 / span 1;
      align-self: start;
      transform: unset;
      i {
        font-size: 30px;
      }
    }
  }
}
