.intelslider {
  position: relative;
  inline-size: 100%;
  .track {
    --slides: 3;
    --gap: 20px;
    --gaps: calc(var(--gap) * (var(--slides) - 1));
    overflow: hidden;
    display: flex;
    column-gap: var(--gap);
    scroll-behavior: smooth;
    .slide {
      width: calc((100% - var(--gaps)) / var(--slides));
      flex-shrink: 0;
    }
  }
  .controls {
    --arw-w: 13px;
    position: absolute;
    inset-inline-end: calc(0px - var(--arw-w));
    inset-block-start: 50%;
    transform: translateY(-50%);
    display: flex;
    justify-content: space-between;
    inline-size: calc(100% + var(--arw-w) * 2);
    pointer-events: none;
    .prev,
    .next {
      aspect-ratio: 1 / 1.923;
      inline-size: var(--arw-w);
      block-size: auto;
      background-repeat: no-repeat;
      background-size: 100%;
      background-position: center;
      transition: opacity var(--tr);
      pointer-events: auto;
    }
    .prev:hover,
    .next:hover {
      opacity: .5;
    }
    .prev {
      background-image: url('../img/svg/icons/arrow-prev.svg');
    }
    .next {
      background-image: url('../img/svg/icons/arrow-next.svg');
    }
  }
}
