.pop-subscribe-wrap {
  max-inline-size: 556px;
  .close {
    right: 17px;
    top: 13px;
  }
}

.pop-subscribe {
  display: grid;
  justify-items: center;
  inline-size: 100%;
  padding: 45px clamp(15px, calc(0.70rem + 1.17vw), 30px);
  ;
  background-color: var(--clr-bg);
  border-radius: 20px;
  outline: 1px solid var(--clr-dark);
  outline-offset: -7px;
  &__title {
    max-inline-size: clamp(295px, calc(16.41rem + 10.16vw), 425px);
    margin-block-end: clamp(15px, calc(0.86rem + 0.39vw), 20px);
    font-size: clamp(21px, calc(1.17rem + 0.70vw), 30px);
    font-weight: var(--fw-b);
    line-height: 1.4;
    text-align: center;
    text-transform: uppercase;
  }
  &__txt {
    margin-block-end: clamp(25px, calc(1.41rem + 0.78vw), 35px);
    max-inline-size: 386px;
    font-size: clamp(14px, calc(0.81rem + 0.31vw), 18px);
    line-height: 1.4;
    text-align: center;
  }
  &__form {
    display: grid;
    gap: clamp(10px, calc(0.55rem + 0.39vw), 15px);
    inline-size: 100%;
    margin-block-end: 10px;
    label {
      display: none;
    }
  }
  &__input {
    display: flex;
    justify-content: center;
    inline-size: 100%;
    padding: 0 30px;
    block-size: clamp(44px, calc(2.66rem + 0.47vw), 50px);
    background-color: transparent;
    font-size: clamp(15px, calc(0.89rem + 0.23vw), 18px);
    font-weight: var(--fw-m);
    border: 1px solid var(--clr-dark);
    border-radius: 10px;
    outline: none;
  }
  &__input:focus-visible {
    outline: 1px solid var(--clr-dark);
    outline-offset: -2px;
  }
  &__input::placeholder {
    color: var(--clr-sec);
    font-family: var(--ff-sec);
  }
  &__input:focus-visible::placeholder {
    color: transparent;
  }
  .btn {
    block-size: clamp(44px, calc(2.58rem + 0.86vw), 55px);
    font-size: clamp(15px, calc(0.86rem + 0.39vw), 20px);
  }
  &__agreement {
    max-inline-size: 361px;
    color: var(--clr-sec);
    font-size: 14px;
    text-align: center;
  }
}
