.product-card {
  --qv-w: 65px;
  --qv-h: 37px;
  position: relative;
  display: flex;
  flex-direction: column;
  block-size: 100%;
  color: var(--clr-card);
  background-color: var(--clr-white);
  border: 1px solid var(--clr-light-green);
  border-radius: 20px;
  transition: transform .2s ease-in-out, opacity .2s ease-in-out;
  &__top {
    position: relative;
    block-size: 203px;
    img {
      block-size: 100%;
      border-radius: 20px 20px 0 0;
    }
  }
  &__top::before {
    content: '';
    display: block;
    inline-size: var(--qv-w);
    block-size: var(--qv-h);
    position: absolute;
    z-index: 0;
    inset-inline-start: 0;
    inset-block-start: 0;
    background-color: var(--clr-light-green);
    border-radius: 200vw 0 0 0;
    opacity: 0;
    transition: opacity var(--tr);
  }
  &__fav {
    position: absolute;
    z-index: 1;
    inset-inline-start: 0;
    inset-block-start: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    inline-size: var(--qv-w);
    block-size: var(--qv-h);
    color: var(--clr-light-green);
    background-color: var(--clr-white);
    font-size: 28px;
    border-radius: 20px 0 20px 0;
    .hidden {
      display: none;
    }
  }
  &__quick-view {
    overflow: hidden;
    position: absolute;
    z-index: 0;
    inset-inline-start: var(--qv-w);
    inset-block-start: 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    inline-size: 0;
    block-size: 37px;
    padding-inline-end: 0px;
    color: var(--clr-white);
    background-color: var(--clr-light-green);
    font-size: 14px;
    font-weight: var(--fw-b);
    text-align: center;
    border-radius: 0 100vw 100vw 0;
    opacity: 0;
    transition: inline-size var(--tr), opacity var(--tr);
  }
  img.hidden {
    display: none;
  }
  &__btm {
    position: relative;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    padding: 15px 30px 12px 15px;
  }
  &__title {
    margin-block-end: 10px;
    font-size: 17px;
    font-weight: var(--fw-b);
  }
  &__price {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    column-gap: 10px;
    row-gap: 0px;
    margin-block-start: auto;
    margin-block-end: 20px;
    font-size: 20px;
    font-weight: var(--fw-b);
    color: var(--clr-price);
    .price-old {
      color: var(--clr-sec);
      font-size: 17px;
      font-weight: var(--fw-r);
      text-decoration-line: line-through;
    }
    .price-old.hidden {
      display: none;
    }
  }
  &__nutr-value {
    display: none;
  }
  &__package-weight {
    display: none;
  }
  &__weight {
    display: none;
  }
  &__package {
    font-size: 17px;
    opacity: .5;
  }
  &__extra {
    position: absolute;
    z-index: 3;
    inset-inline-end: -5px;
    inset-block-end: 30px;
    display: flex;
    flex-direction: column;
    gap: 2px;
    text-align: center;
  }
  &__extra.hidden {
    display: none;
  }
  &__order {
    display: flex;
    gap: 8px;
    margin-block-start: 20px;
  }
  &__order-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    inline-size: 100%;
    block-size: 32px;
    padding: 0;
    font-size: 14px;
    line-height: 1;
  }
  &__count {
    display: flex;
    align-items: center;
    inline-size: 150px;
    padding-inline: 10px;
    background-color: #EAEAEA;
    border-radius: 100vw;
    i {
      cursor: pointer;
    }
  }
  &__input {
    inline-size: 100%;
    background-color: transparent;
    font-size: 20px;
    text-align: center;
    border: none;
  }
  &__input:focus-visible {
    outline: none;
  }
  &__order.hidden {
    display: none;
  }
  &__terms,
  &__period,
  &__provider-img,
  &__provider-text,
  &__provider-prod {
    display: none;
  }
  &__contacts-list {
    display: none;
  }
}

.product-card::before {
  content: '';
  display: block;
  position: absolute;
  z-index: 2;
  inset: 0;
  border: 2px solid transparent;
  border-radius: 18px;
  pointer-events: none;
  transition: border-color var(--tr);
}

.product-card:hover {
  .product-card__quick-view {
    inline-size: calc(100% - var(--qv-w));
    opacity: 1;
  }
  .product-card__top::before {
    opacity: 1;
  }
}

.product-card:hover::before {
  border-color: var(--clr-light-green);
}

.product-card.hidden {
  position: absolute;
  visibility: hidden;
  transform: scale(0);
  opacity: 0;
}

@media (max-width: 600px) {
  .product-card {
    &__count {
      inline-size: unset;
    }
  }
}

@media (max-width: 480px) {
  .product-card {
    --qv-w: 40px;
    --qv-h: 30px;
    &__top {
      block-size: 130px;
    }
    &__fav {
      font-size: 21px;
    }
    &__quick-view {
      block-size: 30px;
      font-size: 10px;
    }
    .icon-status {
      inset-inline-end: -4px;
      inset-block-start: 32px;
      block-size: 29px;
    }
    &__btm {
      padding: 10px 25px 10px 10px;
    }
    &__title {
      font-size: 14px;
    }
    &__price {
      margin-block-end: 10px;
      font-size: 15px;
      .price-old {
        font-size: 12px;
      }
    }
    &__extra {
      inset-inline-end: -4px;
      inset-block-end: 20px;
      gap: 2px;
    }
    &__package {
      font-size: 12px;
    }
    &__order {
      flex-direction: column;
      gap: 6px;
    }
    &__order-btn {
      order: 1;
      display: flex;
      justify-content: center;
      align-items: center;
      inline-size: 100%;
      block-size: 32px;
      padding: 0;
      font-size: 14px;
      line-height: 1;
    }
    &__count {
      order: 0;
      display: flex;
      align-items: center;
      padding-inline: 10px;
      background-color: #EAEAEA;
      border-radius: 100vw;
    }
  }
}
