.tabs__content {
  position: relative;
}

.tabs__panel {
  // display: none
  /*----------------------------*/
  position: absolute;
  z-index: -1;
  left: 0;
  top: 0;
  width: 100%;
  visibility: hidden;
  opacity: 0;
  transform: translateX(-12%);
  transition: opacity var(--tr), transform var(--tr);
}

.tabs__nav {
  list-style: none;
  display: flex
}

.tabs__panel--active {
  // display: block;
  /*----------------------------*/
  position: relative;
  z-index: 10;
  visibility: visible;
  opacity: 1;
  transform: translateX(0%);
}

.tabs__nav-btn:focus {
  color: #fff;
  background-color: navy
}

.tabs__nav-btn--active {
  background-color: wheat
}
