.order {
  display: grid;
  grid-template-columns: repeat(9, 1fr);
  column-gap: 30px;
  row-gap: 40px;
  &__item:not(.order__attention) {
    padding: 20px 30px;
    background-color: var(--clr-white);
    border-radius: 20px;
    box-shadow: 0px 0px 20px 0px rgba(138, 133, 116, 0.38);
  }
  &__way {
    grid-column: 1 / span 6;
    grid-row: 3 / span 1;
    background-image: url('../img/misc/footer-bg.webp');
    background-repeat: no-repeat;
    background-position: right 15px bottom 15px;
    background-size: 180px 152px;
  }
  &__way.bg-changed {
    background-size: 199px 168px;
  }
  &__title-block {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: space-between;
    margin-block-end: 30px;
  }
  &__title {
    display: flex;
    align-items: center;
    gap: 10px;
    color: var(--clr-dark);
    font-size: clamp(18px, calc(17.44px + 0.16vw), 20px);
    font-weight: var(--fw-b);
    i {
      color: var(--clr-dark);
      font-size: 26px;
      transform: translateX(-8px);
    }
  }
  &__title-block-btns {
    display: flex;
    align-items: center;
    gap: 20px;
    a:link,
    a:visited,
    a:hover,
    a:active {
      color: var(--clr-dark);
    }
  }
  &__title-block-btns>*:hover {
    text-decoration: underline;
  }
  &__way-radios {
    position: relative;
    display: flex;
    align-items: center;
    max-inline-size: 435px;
    block-size: 37px;
    border: 1px solid var(--clr-dark);
    border-radius: 10px;
    label {
      display: flex;
      justify-content: center;
      align-items: center;
      block-size: 100%;
      padding-inline: 30px;
      background-repeat: no-repeat;
      background-position: center;
      background-size: 110% 120%;
      color: var(--clr-dark);
      font-size: 14px;
      font-weight: var(--fw-b);
      text-align: center;
      border-radius: 10px;
      cursor: pointer;
    }
    label.active {
      color: var(--clr-white);
      background-image: url('../img/misc/wood-texture.webp');
    }
  }
  &__way-address {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    // gap: 30px;
    margin-block-end: 30px;
  }
  &__way-address.hidden {
    display: none;
  }
  &__way-address-item {
    display: flex;
    flex-direction: column;
    label {
      margin-block-end: 3px;
      font-size: 14px;
    }
    input {
      inline-size: 100%;
      padding-block-end: 4px;
      color: var(--clr-dark);
      background-color: transparent;
      font-size: 14px;
      font-weight: var(--fw-b);
      border: none;
      border-block-end: 1px solid var(--clr-sec);
      outline: none;
    }
  }
  &__way-address-item:nth-child(2) {
    grid-column: 1 / span 9;
    grid-row: 2 / span 1;
  }
  // &__way-address-item:nth-child(2).full-length {
  //   grid-column: 1 / span 9;
  // }
  // &__way-address-item:nth-child(2) {
  //   grid-column: 1 / span 3;
  //   grid-row: 2 / span 1;
  // }
  // &__way-address-item:nth-child(3) {
  //   grid-column: 4 / span 3;
  //   grid-row: 2 / span 1;
  // }
  // &__way-address-item:nth-child(4) {
  //   grid-column: 7 / span 3;
  //   grid-row: 2 / span 1;
  // }
  // &__way-address-item:nth-child(5) {
  //   grid-column: 1 / span 3;
  //   grid-row: 3 / span 1;
  // }
  // &__way-address-item:nth-child(6) {
  //   grid-column: 4 / span 3;
  //   grid-row: 3 / span 1;
  // }
  // &__way-address-item:nth-child(7) {
  //   grid-column: 7 / span 3;
  //   grid-row: 3 / span 1;
  // }
  &__way-datepicker {
    grid-column: 1 / span 12;
    grid-row: 1 / span 1;
    inline-size: 300px;
    margin-block-end: 30px;
  }
  &__way-datepicker {
    --dp-h: 40px;
    position: relative;
    z-index: 1;
    display: flex;
    align-items: center;
    // inline-size: 269px;
    block-size: var(--dp-h);
    padding-inline-start: 10px;
    border: 2px solid var(--clr-dark-green);
    border-radius: 5px;
    cursor: pointer;
    i:nth-child(1) {
      position: relative;
      z-index: 1;
      flex-grow: 1;
      color: var(--clr-dark-green);
      font-size: 28px;
      pointer-events: none;
    }
    i:nth-child(4) {
      position: relative;
      z-index: 1;
      display: flex;
      justify-content: center;
      align-items: center;
      inline-size: calc(var(--dp-h) - 4px);
      block-size: 110%;
      color: var(--clr-dark-green);
      background-color: transparent;
      font-size: 21px;
      border-bottom: 2px solid;
      border-radius: 5px 5px 0 0;
      pointer-events: none;
      transform: rotate(90deg);
      transition: color var(--tr), background-color var(--tr);
    }
    input {
      position: absolute;
      z-index: 0;
      inset: 0;
      inline-size: 100%;
      block-size: 100%;
      padding-inline-start: 45px;
      color: var(--clr-dark-green);
      font-size: 14px;
      font-weight: var(--fw-b);
      border: none;
      outline: none;
      background-color: transparent;
      cursor: pointer;
    }
    input::placeholder {
      color: var(--clr-dark-green);
      font-size: 14px;
      font-weight: var(--fw-b);
    }
    input:focus::placeholder {
      color: transparent;
    }
    label {
      display: none;
    }
  }
  &__way-datepicker:hover {
    i:nth-child(4) {
      color: var(--clr-white);
      background-color: var(--clr-dark-green);
    }
  }
  &__way-datepicker.hidden {
    // display: none;
    position: fixed;
    transform: scale(0);
    margin-block-end: 0;
  }
  &__way-shops {
    display: flex;
    flex-wrap: wrap;
    column-gap: 65px;
    row-gap: 15px;
    margin-block-end: 25px;
    label {
      display: flex;
      align-items: center;
      gap: 10px;
      cursor: pointer;
      i {
        transform: translateY(2px);
      }
    }
  }
  &__way-shops.hidden {
    display: none;
  }
  &__way-map {
    overflow: hidden;
    aspect-ratio: 2.4333/1;
    inline-size: 100%;
    max-inline-size: 511px;
    margin-block-end: 30px;
    border-radius: 15px;
  }
  &__way-map.hidden {
    display: none;
  }
  &__products {
    grid-column: 1 / span 6;
    grid-row: 1 / span 1;
    .order__title {
      color: var(--clr-dark-green);
      i {
        color: var(--clr-dark-green);
      }
    }
  }
  &__products-items>*+* {
    border-block-start: 1px solid #C7C0A7;
  }
  &__products-item {
    display: grid;
    grid-template-columns: 1fr 70px 140px 90px;
    font-size: 14px;
    &>:nth-child(3) {
      margin-inline-end: 10px;
    }
    .pr-desc {
      // display: flex;
      // align-items: center;
      display: grid;
      grid-template-columns: min-content min-content 1fr;
      grid-template-rows: min-content min-content;
      align-items: center;
      column-gap: 10px;
      padding-block: 20px;
      i {
        color: var(--clr-light-green);
        cursor: pointer;
      }
      img {
        inline-size: 100px;
        block-size: 70px;
        border: 1px solid var(--clr-light-green);
        border-radius: 10px;
      }
      .except {
        grid-column: 1 / -1;
        margin-block-start: 10px;
        color: var(--clr-red);
        font-size: 12px;
      }
    }
    .pr-title {
      max-inline-size: 140px;
      color: var(--clr-dark);
      font-weight: var(--fw-b);
      cursor: pointer;
    }
    .pr-price {
      align-self: center;
      display: grid;
    }
    .pr-lbl {
      display: none;
      font-size: 14px;
      opacity: .5;
    }
    .pr-cur {
      font-size: 17px;
      font-weight: var(--fw-b);
      color: var(--clr-price);
    }
    .pr-old {
      font-size: 14px;
      color: var(--clr-sec);
      text-decoration-line: line-through;
    }
    .pr-count-wrap {
      justify-self: center;
      align-self: center;
      display: flex;
      flex-direction: column;
      inline-size: 70%;
      .pr-lbl {
        display: none;
      }
    }
    .pr-count {
      display: flex;
      align-items: center;
      padding: 3px 11px;
      background-color: #EAEAEA;
      border-radius: 100vw;
      i {
        color: var(--clr-dark);
        cursor: pointer;
      }
      input {
        inline-size: 100%;
        color: var(--clr-dark);
        background-color: transparent;
        font-size: 20px;
        text-align: center;
        border: none;
        outline: none;
      }
    }
    // .pr-amount {
    //   justify-self: center;
    //   align-self: center;
    //   inline-size: 120px;
    //   padding-inline: 10px;
    //   .product-card__order {
    //     margin-block-start: unset;
    //   }
    // }
  }
  &__products-quick-order {
    display: block;
    color: var(--clr-dark);
    text-align: right;
  }
  &__products-quick-order:hover {
    text-decoration: underline;
  }
  &__attention {
    .order__title {
      gap: 0;
    }
    p:not(.order__title) {
      font-size: 14px;
    }
    p:not(:last-child) {
      margin-block-end: 15px;
    }
  }
  &__attention-pickup.hidden {
    display: none;
  }
  &__attention-deliv.hidden {
    display: none;
  }
  &__attention-ex-deliv.hidden {
    display: none;
  }
  &__what-to-do {
    grid-column: 1 / span 6;
    grid-row: 2 / span 1;
    .what-to-do__radios {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      gap: 20px;
      label {
        display: flex;
        align-items: center;
        gap: 10px;
        cursor: pointer;
      }
    }
  }
  &__contacts {
    grid-column: 1 / span 6;
    grid-row: 4 / span 1;
    .contacts-person {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-template-rows: 1fr 1fr;
      column-gap: 45px;
      row-gap: 20px;
    }
    .contacts-person__item {
      display: flex;
      flex-direction: column;
      label {
        font-size: 14px;
        margin-block-end: 4px;
      }
      textarea {
        border: none;
        overflow: auto;
        outline: none;
        box-shadow: none;
      }
      input,
      textarea {
        block-size: 25px;
        // padding-block-end: 4px;
        font-size: 14px;
        font-weight: var(--fw-b);
        outline: none;
        border: none;
        border-block-end: 1px solid var(--clr-sec);
      }
    }
  }
  &__summary {
    position: sticky;
    top: 70px;
    grid-column: 7 / span 3;
    grid-row: 1 / span 3;
    align-self: start;
    .btn {
      inline-size: 100%;
      margin-block-end: 10px;
    }
  }
  &__summary-prod-num {
    display: flex;
    justify-content: space-between;
    margin-block-end: 14px;
    font-size: 17px;
    font-weight: var(--fw-b);
  }
  &__summary-prod-delivery {
    display: flex;
    justify-content: space-between;
    margin-block-end: 4px;
    font-size: 17px;
    font-weight: var(--fw-b);
  }
  &__summary-prod-delivery.hidden {
    display: none;
  }
  &__summary-prod-ex-delivery {
    display: flex;
    justify-content: space-between;
    margin-block-end: 4px;
    font-size: 17px;
    font-weight: var(--fw-b);
  }
  &__summary-prod-ex-delivery.hidden {
    display: none;
  }
  .ruble.hidden {
    display: none;
  }
  &__summary-prod-extra {
    margin-block-end: 20px;
    font-size: 14px;
  }
  &__summary-prod-extra.hidden {
    display: none;
  }
  &__summary-prod-sum {
    display: flex;
    justify-content: space-between;
    margin-block-end: 20px;
    padding-block-end: 20px;
    font-size: 20px;
    font-weight: var(--fw-b);
    border-block-end: 1px solid var(--clr-sec);
  }
  &__summary-prod-points-all {
    margin-block-end: 10px;
    font-size: 14px;
    .prod-points {
      font-weight: var(--fw-b);
    }
  }
  &__summary-prod-points-off {
    margin-block-end: 20px;
    padding-block-end: 20px;
    font-size: 14px;
    border-block-end: 1px solid var(--clr-sec);
    input {
      display: inline-block;
      inline-size: 70px;
      margin-inline: 8px;
      padding: 1px 15px;
      background-color: #EAEAEA;
      text-align: center;
      border-radius: 100vw;
      border: none;
      outline: none;
    }
    input:focus::placeholder {
      color: transparent;
    }
    .prod-points-off-all {
      display: inline-block;
      margin-inline-start: 15px;
      padding: 1px 10px 2px;
      color: var(--clr-white);
      background-color: var(--clr-light-green);
      font-weight: var(--fw-b);
      border-radius: 100vw;
      cursor: pointer;
      transition: background-color var(--tr);
      &:hover {
        background-color: var(--clr-dark-green);
      }
    }
  }
  &__summary-prod-sum-discount {
    display: flex;
    justify-content: space-between;
    margin-block-end: 20px;
    font-size: 26px;
    font-weight: var(--fw-b);
    text-transform: uppercase;
  }
  &__summary-info {
    font-size: 12px;
    opacity: .5;
    text-align: center;
    a {
      text-decoration: underline;
    }
  }
}

@media (max-width: 1630px) {
  .order {
    &__way {
      background-image: none;
    }
  }
}

