    grid-template-columns: 1fr;
    column-gap: 0;
    &__way {
      background-image: url('../img/misc/footer-bg.webp');
    }
    &__summary {
      grid-column: 1 / -1;
      grid-row: 5 / span 1;
      .btn {
        inline-size: unset;
      }
    }
  }
}

