        }
      }
      .pr-title {
        max-inline-size: unset;
      }
      .pr-lbl {
        display: block;
      }
      .pr-price:last-child {
        justify-self: end;
        min-inline-size: 90px;
      }
      .pr-count-wrap {
        .pr-lbl {
          display: block;
          text-align: center;
        }
      }
    }
  }
}

@media (max-width: 600px) {
  .order {
    &__way {
      background-position: right -5px bottom -10px;
      background-size: 188px 159px;
    }
    &__way-address {
      max-inline-size: 100%;
    }
    &__summary-prod-extra {
      max-inline-size: 265px;
    }
  }
}

@media (max-width: 500px) {
  .order {
    &__item:not(.order__attention) {
      padding: 20px 15px;
    }
    &__products-item {
      .pr-count-wrap {
        inline-size: 100%;
      }
    }
    &__way-address {
      row-gap: 0px;
    }
    &__contacts {
      grid-column: 1 / span 6;
      grid-row: 4 / span 1;
      .contacts-person {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr 1fr 1fr;
        column-gap: 0;
      }
    }
  }
}

@media (max-width: 440px) {
  .order {
    &__way-address {
      column-gap: 10px;
    }
    &__products-item {
      .pr-price:last-child {
        min-inline-size: 80px;
      }
      .pr-price__cur {
        font-size: 16px;
      }
    }
    &__way-radios label {
      padding-inline: 10px;
    }
  }
}
