.container {
  --p-i: 16px;
  position: relative;
  inline-size: min(var(--max-container) + (var(--p-i) * 2), 100%);
  margin: 0 auto;
  padding-inline: var(--p-i);
  @media (max-width: 1000px) {
    --p-i: 10px;
  }
}

.auto-grid {
  --min-w: 250px;
  --max-w: 1fr;
  --c-g: 30px;
  --r-g: 30px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(min(var(--min-w), 100%), var(--max-w)));
  column-gap: var(--c-g);
  row-gap: var(--r-g);
}

.subtitle-1 {
  max-inline-size: 785px;
  margin-block-end: clamp(24px, calc(21.28px + 0.86vw), 35px);
  font-size: clamp(14px, calc(13.28px + 0.23vw), 17px);
  font-weight: var(--fw-b);
  font-style: italic;
  line-height: 1.4;
  letter-spacing: .5px;
}

.subtitle-2 {
  color: var(--clr-dark-green);
  font-size: 17px;
  font-weight: var(--fw-b);
  line-height: 1.4;
}

.share {
  position: absolute;
  inset-inline-end: 0;
  inset-block-start: 0;
  color: var(--clr-dark);
  font-size: 14px;
  text-decoration: underline;
  @media (max-width: 1000px) {
    inset-block-start: 48px;
  }
  @media (max-width: 600px) {
    inset-block-start: 120px;
  }
}

.share:hover {
  text-decoration: none;
}

.dib {
  display: inline-block;
}

.p-0 {
  padding: 0 !important;
}

.pb-5 {
  padding-block-end: 5px;
}

.m-0 {
  margin: 0 !important;
}

.mt-15 {
  margin-block-start: 15px;
}

.mb-15 {
  margin-block-end: 15px;
}

.mb-30 {
  margin-block-end: 30px;
}

.mb-40 {
  margin-block-end: 40px;
}

.tac {
  text-align: center;
}

.clr-accent {
  color: var(--clr-accent);
}

.bg-white {
  background-color: var(--clr-white) !important;
}

.red {
  color: var(--clr-red);
}

.uc {
  text-transform: uppercase;
}

.error {
  padding-block-start: 3px;
  color: var(--clr-red);
  font-size: 14px;
  // font-weight: var(--fw-b);
  line-height: 1;
}

.fixed {
  overflow-y: hidden;
}

.visually-hidden {
  position: fixed;
  transform: scale(0);
}

.extra-item {
  position: relative;
  display: block;
  inline-size: 33px;
  block-size: 23px;
  color: var(--clr-light);
  background-image: url('../img/misc/extra-item-bg.webp');
  background-repeat: no-repeat;
  background-size: 100% 100%;
  font-size: 18px;
}

.extra-item.hidden {
  display: none;
}

.extra-item.icon-lightning span {
  display: none;
}

.extra-item.time .time-short {
  display: inline-block;
  padding-block-start: 2px;
  font-family: var(--ff-prim);
  font-size: 11px;
  font-weight: var(--fw-b);
  font-style: normal;
  transform: translateY(-5px);
}

.extra-item.time .time-long {
  display: none;
}

.extra-item::after {
  content: attr(data-info);
  position: absolute;
  inset-inline-end: 35px;
  inset-block-start: 0px;
  display: block;
  inline-size: max-content;
  padding: 5px 8px;
  color: var(--clr-dark);
  background-color: var(--clr-light);
  font-family: var(--ff-sec);
  font-size: 10px;
  font-weight: var(--fw-m);
  font-style: normal;
  border-radius: 4px;
  pointer-events: none;
  opacity: 0;
  transition: opacity var(--tr);
}

.extra-item:hover::after {
  opacity: 1;
}

.icon-status-block {
  position: absolute;
  z-index: 2;
  inset-inline-end: -5px;
  inset-block-start: 40px;
  display: flex;
  align-items: end;
  flex-direction: column;
  gap: 0px;
}

.icon-status {
  display: block;
  block-size: 39px;
  background-repeat: no-repeat;
  background-size: auto 100%;
  background-position: right center;
}

.icon-status.hidden {
  display: none;
}

.icon-status.hit {
  inline-size: 80px;
  background-image: url('../img/svg/icons/icon-hit.svg');
}

.icon-status.new {
  inline-size: 110px;
  background-image: url('../img/svg/icons/icon-new.svg');
}

.icon-status.day-prod {
  inline-size: 128px;
  background-image: url('../img/svg/icons/icon-day-prod.svg');
}

@media (max-width: 480px) {
  .extra-item {
    aspect-ratio: 1.4347 / 1;
    inline-size: 23px;
    block-size: auto;
    font-size: 13px;
  }
  .extra-item.time .time-short {
    font-size: 8px;
    transform: translateY(-3px);
  }
  .extra-item::after {
    inset-inline-end: 25px;
  }
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}
