.container-modal {
  padding: 0;
  background-color: transparent;
  border: none;
  .modal__inner {
    position: relative;
    max-inline-size: 50rem;
    padding: 2rem;
    background-color: white;
    border-radius: 1rem;
  }
  .btn-modal-close {
    fill: #444;
    position: absolute;
    width: 24px;
    height: 24px;
    right: 1rem;
    top: 1rem;
    display: block;
    transform: scale(2);
    cursor: pointer;
  }
}

.container-modal::backdrop {
  background-color: hsl(215 62% 19% / .6);
}
