.footer {
  grid-area: footer;
  position: relative;
  z-index: 0;
  display: grid;
  grid-template-areas: '. footinner .';
  grid-template-columns: 1fr min(1600px, 100%) 1fr;
  color: var(--clr-white);
  background-color: var(--clr-light-green);
  a {
    color: inherit;
  }
  &__inner {
    grid-area: footinner;
    display: flex;
    justify-content: space-between;
    gap: 30px;
    margin-inline-start: 408px;
    margin-inline-start: clamp(370px, calc(286.72px + 8.33vw), 420px);
    padding: 40px var(--pad-right) 40px 0;
    background: no-repeat url('../img/misc/footer-bg.webp') right var(--pad-right) bottom;
  }
  &__copyright {
    display: flex;
    flex-direction: column;
    gap: 20px;
    i {
      font-size: 19px;
    }
  }
  &__copyright-text {
    display: flex;
    align-items: center;
    gap: 5px;
    font-size: 14px;
    line-height: 1.35;
  }
  &__copyright-agreement {
    inline-size: min(200px, 100%);
    a {
      font-size: 15px;
      font-size: clamp(13px, calc(9.6px + 0.33vw), 15px);
      line-height: 1.35;
      text-decoration: underline;
      opacity: 0.7;
      transition: opacity var(--tr);
      &:hover {
        opacity: 1;
      }
    }
  }
  &__contacts {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  &__contacts-phone,
  &__contacts-email {
    display: flex;
    align-items: center;
    gap: 7px;
    font-size: 17px;
    font-size: clamp(16px, calc(14.4px + 0.17vw), 16.96px);
    font-weight: var(--fw-b);
    transition: opacity var(--tr);
    i {
      font-size: 20px;
    }
    &:hover {
      opacity: .7;
    }
  }
  &__socials {
    display: flex;
    gap: 10px;
    margin-block-start: 10px;
    a {
      display: flex;
      justify-content: center;
      align-items: center;
      inline-size: 32px;
      block-size: 32px;
      color: var(--clr-light-green);
      background-color: var(--clr-white);
      border-radius: 50%;
      transition: opacity var(--tr);
      i {
        font-size: 24px;
      }
      &:hover {
        opacity: .7;
      }
    }
  }
  &__nav {
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: 3fr 2fr;
    grid-template-rows: repeat(4, min-content);
    max-inline-size: 345px;
    inline-size: 100%;
    row-gap: 20px;
    a {
      font-size: 14px;
      line-height: 1.3;
    }
    a span {
      position: relative;
      display: inline-block;
    }
    a span::before {
      content: '';
      position: absolute;
      inset-inline-start: 0;
      inset-block-end: -2px;
      display: block;
      inline-size: 0%;
      block-size: 1px;
      background-color: var(--clr-white);
      transition: inline-size var(--tr);
    }
    a span:hover::before {
      inline-size: 100%;
    }
  }
  &__intrid {
    align-self: end;
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-inline-size: 140px;
    block-size: 26px;
    padding-inline: 6px;
    color: var(--clr-light-green);
    background-color: var(--clr-white);
    border-radius: 13px;
  }
  &__intrid:hover {
    .footer__intrid-circle {
      background-color: var(--clr-dark);
    }
    .footer__intrid-text {
      color: var(--clr-dark);
    }
  }
  &__intrid-circle {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    aspect-ratio: 1 / 1;
    block-size: 18px;
    display: block;
    background-color: var(--clr-light-green);
    border-radius: 100%;
    transition: background-color var(--tr);
    &--in {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      display: block;
      inline-size: 6px;
      block-size: 6px;
      background-color: var(--clr-white);
      border-radius: 100%;
    }
  }
  &__intrid-text {
    font-size: 12px;
    font-family: var(--ff-sec);
    font-weight: var(--fw-m);
    color: var(--clr-light-green);
    transition: color var(--tr);
  }
}

@media (max-width: 1320px) {
  .footer {
    &__inner {
      display: grid;
      grid-template-areas: 'nav nav' 'contacts contacts' 'copyright intrid';
      grid-template-columns: 1fr min-content;
      gap: 20px;
      padding: 40px 16px;
    }
    &__nav {
      grid-area: nav;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      column-gap: 46px;
      row-gap: 10px;
      max-inline-size: unset;
    }
    &__contacts {
      grid-area: contacts;
      flex-direction: row;
      justify-content: space-between;
    }
    &__socials {
      margin-block-start: 0;
    }
    &__copyright {
      grid-area: copyright;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      max-inline-size: 540px;
      max-inline-size: clamp(383px, calc(-107.68px + 49.06vw), 540px);
    }
    &__copyright-agreement br {
      display: none;
    }
    &__intrid {
      grid-area: intrid;
    }
  }
}

@media (max-width: 1000px) {
  .footer {
    &__inner {
      margin-inline-start: unset;
      padding-block-end: 70px;
    }
    &__nav {
      justify-content: space-between;
      column-gap: 16px;
    }
    &__copyright {
      max-inline-size: clamp(415px, calc(64.96px + 50.00vw), 565px);
    }
  }
}

@media (max-width: 700px) {
  .footer {
    &__inner {
      grid-template-areas: 'copyright' 'intrid';
      grid-template-columns: 1fr;
    }
    &__nav {
      display: none;
    }
    &__nav {
      display: none;
    }
    &__contacts {
      display: none;
    }
    &__copyright {
      flex-wrap: wrap;
      justify-content: center;
      max-inline-size: unset;
    }
    &__copyright-agreement {
      a {
        font-size: 15px;
      }
    }
    &__intrid {
      justify-self: center;
    }
  }
}
