.pop-qv {
  display: grid;
  grid-template-columns: 2fr 1fr;
  grid-template-rows: 1fr min-content;
  column-gap: 30px;
  row-gap: 20px;
  inline-size: 100%;
  padding: 30px;
  background-color: var(--clr-white);
  border-radius: 20px;
  &__details {
    grid-column: 1 / span 1;
    grid-row: 1 / span 2;
    display: flex;
    align-items: start;
    column-gap: 50px;
  }
  .swiper-container {
    --arr-w: 13px;
    --space: 4px;
    max-inline-size: 347px;
    .swiper-controls {
      width: calc(100% + var(--arr-w) * 2 + (var(--space) * 2));
      transform: translateX(calc(0px - var(--arr-w) - var(--space)));
    }
  }
  &__slide {
    position: relative;
    // overflow: hidden;
    position: relative;
    block-size: 100%;
    margin-inline: 5px;
    border: 1px solid var(--clr-light-green);
    border-radius: 20px;
    img {
      block-size: 100%;
      border-radius: 20px;
      cursor: pointer;
    }
  }
  &__fav {
    position: absolute;
    z-index: 2;
    inset-inline-start: 6px;
    inset-block-start: 1px;
    display: flex;
    justify-content: center;
    align-items: center;
    inline-size: 65px;
    block-size: 37px;
    color: var(--clr-light-green);
    background-color: var(--clr-white);
    font-size: 28px;
    border-radius: 20px 0 20px 0;
    .hidden {
      display: none;
    }
  }
  .icon-status-block {
    inset-inline-end: 0px;
    inset-block-start: 15px;
  }
  &__slide::before {
    content: '';
    position: absolute;
    z-index: 1;
    inset: 0;
    display: block;
    border: 3px solid transparent;
    border-radius: 20px;
    pointer-events: none;
    transition: border-color var(--tr);
  }
  &__slide:hover::before {
    border-color: var(--clr-light-green);
  }
  &__goto {
    display: block;
    margin-block-start: 10px;
    color: var(--clr-dark);
    text-align: right;
    text-decoration: underline;
  }
  &__title {
    margin-block-end: 20px;
    color: var(--clr-dark);
    font-size: clamp(18px, calc(13.44px + 1.25vw), 26px);
    font-weight: var(--fw-b);
    line-height: 1.1;
  }
  &__price {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    column-gap: 20px;
    row-gap: 5px;
    margin-block-end: 5px;
    color: var(--clr-price);
    font-size: clamp(18px, calc(13.44px + 1.25vw), 26px);
    font-weight: var(--fw-b);
    line-height: 1.46;
    text-transform: uppercase;
    >span:nth-child(2) {
      color: var(--clr-sec);
      font-size: 21px;
      font-size: clamp(18px, calc(17.28px + 0.23vw), 21px);
      font-style: normal;
      font-weight: var(--fw-r);
      text-decoration-line: line-through;
    }
    >span:nth-child(2).hidden {
      display: none;
    }
  }
  &__info {
    min-inline-size: 270px;
  }
  &__from {
    display: block;
    margin-block-end: 7px;
    color: var(--clr-dark);
    font-size: 15px;
    font-weight: var(--fw-b);
  }
  &__text {
    max-inline-size: 306px;
    color: var(--clr-dark);
    font-size: 14px;
  }
  &__text.pack {
    margin-block-end: 25px;
  }
  &__text.terms {
    margin-block-end: 10px;
  }
  &__order {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
    margin-block-end: 20px;
  }
  &__order-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    inline-size: 100%;
    max-inline-size: 136px;
    block-size: 32px;
    padding: 0;
    font-size: 14px;
    line-height: 1;
  }
  &__count {
    display: flex;
    align-items: center;
    inline-size: 123px;
    padding-inline: 10px;
    background-color: #EAEAEA;
    border-radius: 100vw;
  }
  &__input {
    inline-size: 100%;
    background-color: transparent;
    font-size: 20px;
    text-align: center;
    border: none;
  }
  &__input:focus-visible {
    outline: none;
  }
  &__count {
    i {
      cursor: pointer;
    }
  }
  &__contacts {
    align-self: start;
    grid-column: 2 / span 1;
    grid-row: 1 / span 1;
    inline-size: 100%;
    padding: 10px 20px;
    background-color: var(--clr-white);
    border-radius: 15px;
    box-shadow: 0px 0px 20px 0px rgba(138, 133, 116, 0.38);
  }
  &__heading {
    display: flex;
    gap: 5px;
    margin-block-end: 5px;
    color: var(--clr-light-green);
    font-family: var(--ff-sec);
    font-size: 15px;
    font-weight: var(--fw-sb);
    i {
      font-size: 18px;
    }
  }
  &__list {
    overflow-y: auto;
    overflow-x: hidden;
    block-size: 140px;
    block-size: 100%;
    max-block-size: 140px;
    margin-block-end: 5px;
    padding-inline-end: 15px;
  }
  &__list::-webkit-scrollbar {
    width: 5px;
  }
  &__list::-webkit-scrollbar-track {
    background-color: #F0E6D8;
    border-radius: 100vw;
  }
  &__list::-webkit-scrollbar-thumb {
    background-color: var(--clr-dark);
    border-radius: 100vw;
  }
  &__contacts-item {
    display: flex;
    flex-wrap: wrap;
    row-gap: 5px;
    padding-block: 7px;
    color: var(--clr-light-green);
    font-size: 14px;
    a {
      display: flex;
      align-items: center;
      gap: 3px;
      color: var(--clr-light-green);
      font-size: clamp(12px, calc(10.88px + 0.31vw), 14px);
      i {
        font-size: 13px;
      }
    }
  }
  &__contacts-item span {
    inline-size: 175px;
    font-size: clamp(12px, calc(10.88px + 0.31vw), 14px);
  }
  &__contacts-item:not(:last-child) {
    border-block-end: 1px solid var(--clr-light-green);
  }
  &__view-all {
    display: inline-block;
    margin-block-end: 10px;
    color: var(--clr-dark);
    font-size: 12px;
    text-decoration: underline;
  }
  &__view-all:visited,
  &__view-all:hover,
  &__view-all:active {
    color: var(--clr-dark);
  }
  &__extra {
    display: flex;
    flex-direction: column;
    padding-block-start: 10px;
    color: var(--clr-dark);
    font-size: 14px;
    border-block-start: 1px solid var(--clr-light-green);
    &>span {
      display: flex;
      align-items: center;
      gap: 5px;
      i {
        font-size: 26px;
      }
    }
    a {
      text-decoration: underline;
    }
    a:hover {
      text-decoration: none;
    }
  }
  &__extra span.hidden {
    display: none;
  }
  &__about {
    grid-column: 2 / span 1;
    grid-row: 2 / span 1;
    display: grid;
    grid-template-columns: 74px 1fr;
    gap: 20px;
  }
  &__about-desc {
    display: flex;
    flex-direction: column;
    a {
      text-decoration: underline;
    }
    a:visited,
    a:hover,
    a:active {
      color: var(--clr-dark);
    }
  }
  &__img {
    overflow: hidden;
    inline-size: 74px;
    block-size: 74px;
    border-radius: 100vw;
  }
}

@media (max-width: 1100px) {
  .pop-qv {
    &__about {
      grid-template-columns: 50px 1fr;
      gap: 10px;
    }
    &__about-desc {
      font-size: 12px;
    }
    &__img {
      inline-size: 50px;
      block-size: 50px;
    }
  }
}

@media (max-width: 1000px) {
  .pop-qv {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: min-content min-content;
    column-gap: 30px;
    row-gap: 20px;
    &__details {
      grid-column: 1 / span 2;
      grid-row: 1 / span 1;
    }
    &__text {
      max-inline-size: unset;
    }
    &__contacts {
      grid-column: 1 / span 2;
      grid-row: 2 / span 1;
      display: grid;
      grid-template-columns: 2fr 1fr;
      gap: 40px;
    }
    &__extra {
      padding-block-start: 20px;
      border-block-start-width: 0px;
    }
    &__about {
      grid-column: 1 / span 2;
      grid-row: 3 / span 1;
      grid-template-columns: 74px 1fr;
      align-items: center;
      gap: 20px;
    }
    &__about-desc {
      display: flex;
      flex-direction: column;
      gap: 10px;
      font-size: inherit;
    }
    &__img {
      inline-size: 74px;
      block-size: 74px;
    }
  }
}

@media (max-width: 768px) {
  .pop-qv {
    .terms,
    .period {
      display: none;
    }
    .swiper-container {
      max-inline-size: 270px;
    }
  }
}

@media (max-width: 660px) {
  .pop-qv {
    padding: 20px;
    &__details {
      flex-direction: column;
      row-gap: 15px;
    }
    .swiper-container {
      align-self: center;
    }
    &__title {
      margin-block-end: 15px;
    }
    &__text.pack {
      margin-block-end: 15px;
    }
    &__order {
      margin-block-end: 10px;
    }
    &__contacts {
      display: unset;
      grid-template-columns: 2fr 1fr;
      gap: 40px;
    }
    &__extra {
      border-block-start-width: 1px;
    }
  }
}
