  .uc-deliveries {
    display: grid;
    grid-template-areas: 'title slider subscript';
    grid-template-columns: min-content 523px 1fr;
    gap: 25px;
    margin-block-end: 50px;
    &__title {
      grid-area: title;
      background-image: url('../img/svg/misc/arrow-curved.svg');
      background-repeat: no-repeat;
      background-position: right top 100px;
    }
    &__subscript {
      grid-area: subscript;
      align-self: center;
      display: flex;
      align-items: center;
      gap: 20px;
      padding: 25px 20px 25px 72px;
      background-image: url('../img/svg/icons/icon-subscription.svg');
      background-repeat: no-repeat;
      background-position: 20px center;
      border: 1px solid var(--clr-dark);
      border-radius: 20px;
      a:link,
      a:active {
        display: inline-block;
        border-block-end: 2px solid var(--clr-dark);
        transition: border-color var(--tr);
      }
      a:hover {
        border-color: transparent;
      }
    }
    &__subscript-img {
      inline-size: 32px;
      block-size: 41px;
    }
    .swiper-container {
      grid-area: slider;
      --arr-w: 13px;
      .swiper-controls {
        top: 55%;
      }
    }
  }
  /*------------------------------------------*/

  .uc-deliveries__slider {
    position: relative;
    a {
      overflow: hidden;
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 7px;
      aspect-ratio: 1 / 1;
      inline-size: 159px;
      block-size: auto;
      margin: 0 !important;
      padding-inline: 14px;
      padding-block: 20px;
      color: var(--clr-dark);
      background-color: var(--clr-white);
      background-repeat: no-repeat;
      background-position: center bottom;
      border: 1px solid var(--clr-dark);
      border-radius: 50%;
    }
    a.meat {
      background-image: url('../img/uc-deliveries__slider/meat.webp');
    }
    a.fish {
      background-image: url('../img/uc-deliveries__slider/fish.webp');
    }
    a.text {
      justify-content: space-between;
      time {
        order: 1;
        margin-block-start: auto;
      }
      p {
        order: 0;
      }
    }
    p {
      font-size: 13px;
      font-weight: var(--fw-b);
      text-align: center;
    }
    .title {
      font-size: 16px;
    }
    time {
      font-family: var(--ff-sec);
      font-weight: var(--fw-sb);
    }
    a::before {
      content: '';
      display: block;
      position: absolute;
      z-index: 1;
      inset: 0;
      border: 1px solid transparent;
      border-radius: 100vw;
      pointer-events: none;
      transition: border-color var(--tr);
    }
    a:hover::before {
      border-color: var(--clr-dark)
    }
  }
  /*------------------------------------------*/

  @media (max-width: 1420px) {
    .uc-deliveries {
      grid-template-areas: 'title title' 'slider subscript';
      grid-template-columns: 523px 1fr;
      &__title {
        justify-self: center;
        background-image: unset;
        h2 {
          line-height: 1;
        }
      }
    }
  }

  @media (max-width: 1260px) {
    .uc-deliveries {
      grid-template-columns: 340px 1fr;
      &__subscript {
        gap: 15px;
        padding: 25px 10px 25px 72px;
      }
    }
  }

  @media (max-width: 1050px) {
    .uc-deliveries {
      &__subscript {
        padding: 25px 10px;
        background-image: none;
      }
    }
  }

  @media (max-width: 1000px) {
    .uc-deliveries {
      grid-template-columns: 523px 1fr;
      .swiper-container {
        inline-size: unset;
      }
      &__subscript {
        padding: 25px 10px 25px 72px;
        background-image: url('../img/svg/icons/icon-subscription.svg');
      }
    }
  }

  @media (max-width: 870px) {
    .uc-deliveries {
      grid-template-columns: 330px 1fr;
    }
  }

  @media (max-width: 680px) {
    .uc-deliveries {
      grid-template-columns: 1fr;
      column-gap: 0;
      .swiper-container {
        justify-self: center;
        inline-size: unset;
        max-inline-size: 330px;
      }
      &__subscript {
        display: none;
      }
    }
  }

  @media (max-width: 480px) {
    .uc-deliveries {
      row-gap: 0;
      margin-block-end: 40px;
      .swiper-container {
        max-inline-size: 300px;
      }
      a {
        inline-size: 145px;
        block-size: 145px;
        padding-inline: 6px;
      }
      a.text p {
        inline-size: 110px;
      }
      time {
        font-size: 14px;
      }
      .name {
        font-size: 14px;
      }
      p {
        font-size: 11px;
      }
    }
  }
