.pop-qv.product-block {
  overflow: hidden;
  margin-block-end: 50px;
  padding: 0px 0px 30px 30px;
  box-shadow: 0px 0px 20px 0px rgba(138, 133, 116, 0.38);
  .pop-qv__title {
    display: none;
  }
  .pop-qv__details {
    padding-block-start: 30px;
  }
  .pop-qv__about {
    padding-inline-end: 30px;
  }
  .pop-qv__about-desc {
    display: flex;
    flex-direction: column;
    gap: 5px;
  }
}

@media (max-width: 1500px) {
  .pop-qv.product-block {
    grid-template-columns: 1fr;
    grid-template-rows: min-content min-content min-content;
    column-gap: unset;
    row-gap: 20px;
    padding: 0px 30px 30px 30px;
    .pop-qv__details {
      grid-column: 1 / span 1;
      grid-row: 1 / span 1;
    }
    .pop-qv__text {
      max-inline-size: unset;
    }
    .pop-qv__contacts {
      grid-column: 1 / span 1;
      grid-row: 2 / span 1;
      display: grid;
      grid-template-columns: 1fr 1fr;
      align-items: center;
      column-gap: 40px;
    }
    .pop-qv__extra {
      border-block-start-width: 0;
    }
    .pop-qv__about {
      grid-column: 1 / span 1;
      grid-row: 3 / span 1;
      align-items: center;
    }
  }
}

@media (max-width: 1300px) {
  .pop-qv.product-block {
    .pop-qv__contacts {
      grid-template-columns: 2fr 1fr;
    }
    .pop-qv__info {
      min-inline-size: unset;
    }
  }
}

@media (max-width: 1200px) {
  .pop-qv.product-block {
    .swiper-container {
      max-inline-size: 250px;
    }
  }
}

@media (max-width: 1100px) {
  .pop-qv.product-block {
    .pop-qv__details {
      column-gap: 35px;
    }
    .swiper-container {
      max-inline-size: 200px;
    }
    .pop-qv__contacts {
      column-gap: 20px;
    }
  }
}

@media (max-width: 1000px) {
  .pop-qv.product-block {
    .pop-qv__details {
      column-gap: 50px;
    }
    .swiper-container {
      max-inline-size: 300px;
    }
  }
}

@media (max-width: 660px) {
  .pop-qv.product-block {
    .pop-qv__details {
      row-gap: 20px;
    }
    .pop-qv__info {
      inline-size: 100%;
    }
    .terms,
    .period {
      display: block;
    }
    .pop-qv__contacts {
      grid-template-columns: 1fr;
      row-gap: 0;
    }
    .pop-qv__contacts-item {
      justify-content: space-between;
    }
    .pop-qv__contacts-item span {
      inline-size: unset;
    }
    .pop-qv__extra {
      margin-block-start: 15px;
      padding-block-start: 8px;
      border-block-start-width: 1px;
    }
  }
}

@media (max-width: 400px) {
  .pop-qv.product-block {
    padding: 0px 15px 30px 15px;
    .swiper-container {
      max-inline-size: 250px;
    }
    .pop-qv__contacts {
      padding-inline: 10px;
    }
    .pop-qv__about-desc {
      font-size: 14px;
    }
  }
}
