.pop-login-wrap {
  inline-size: min(536px, 100%);
}

.pop-login {
  inline-size: 100%;
  padding: 40px clamp(10px, 0.2621rem + 1.6129vw, 30px) 45px;
  background-color: var(--clr-bg);
  border-radius: 20px;
  outline: 1px solid var(--clr-dark);
  outline-offset: -5px;
  &__form {
    margin-block-end: 10px;
  }
  &__title {
    margin-block-end: clamp(20px, 1.0685rem + 0.8065vw, 30px);
    font-size: clamp(21px, 1.1492rem + 0.7258vw, 30px);
    font-weight: var(--fw-b);
    text-align: center;
    text-transform: uppercase;
  }
  .form-group {
    margin-block-end: clamp(10px, 0.5343rem + 0.4032vw, 15px);
    label {
      display: none;
    }
  }
  &__input {
    inline-size: 100%;
    padding: 12px clamp(20px, 1.0685rem + 0.8065vw, 30px);
    background-color: transparent;
    font-family: var(--ff-sec);
    font-size: clamp(15px, 0.8831rem + 0.2419vw, 18px);
    border: 1px solid var(--clr-dark);
    border-radius: 10px;
  }
  &__input:focus-visible {
    outline: 1px solid var(--clr-dark);
    outline-offset: -2px;
  }
  &__input::placeholder {
    color: var(--clr-sec);
  }
  &__input:focus-visible::placeholder {
    color: transparent;
  }
  &__links {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-block-end: 20px;
    a,
    a:visited {
      color: var(--clr-dark);
      font-size: 14px;
      text-decoration: underline;
    }
    a:hover {
      text-decoration: none;
    }
  }
  .btn {
    display: block;
    inline-size: 100%;
    font-size: clamp(15px, 0.8468rem + 0.4032vw, 20px);
  }
  &__agreement {
    color: var(--clr-sec);
    font-size: 14px;
    text-align: center;
  }
}
