.btn {
  padding: 10px 67px;
  color: var(--clr-white);
  background-color: var(--clr-light-green);
  border-radius: 100vw;
  transition: background-color var(--tr);
}

.btn:hover,
.btn:active,
.btn:focus-visible {
  background-color: var(--clr-dark-green);
}

.btn:disabled {
  color: var(--clr-black);
  background-color: #EAEAEA;
}
