.header {
  grid-area: header;
  position: relative;
  z-index: 2;
  margin-block-end: 10px;
  &__top {
    overflow: hidden;
    display: grid;
    grid-template-areas: '. nav .';
    grid-template-columns: 1fr min(1600px, 100%) 1fr;
    background-color: var(--clr-light-green);
  }
  &__mainlinks {
    display: none;
  }
  &__nav {
    grid-area: nav;
    display: flex;
    flex-wrap: wrap;
    justify-content: end;
    align-items: center;
    // column-gap: 40px;
    column-gap: clamp(15px, calc(-26.72px + 4.17vw), 40px);
    row-gap: 8px;
    padding-inline-end: var(--pad-right);
    padding-block: 11px;
    transform: translateX(0%);
    opacity: 1;
    transition: transform .6s ease-in-out;
  }
  &__nav.hidden {
    transform: translateX(100%);
    opacity: 0;
    transition: transform .6s ease-in-out;
  }
  &__nav-item {
    color: var(--clr-white);
    font-size: clamp(14px, calc(9.92px + 0.38vw), 16px);
    font-size: 13px;
    font-size: clamp(13px, calc(8px + 0.50vw), 16px);
    line-height: var(--fz-base);
    span {
      position: relative;
    }
    span::before {
      content: '';
      position: absolute;
      inset-inline-start: 0;
      inset-block-end: 0;
      display: block;
      inline-size: 0%;
      block-size: 1px;
      background-color: var(--clr-white);
      transition: inline-size var(--tr);
    }
  }
  &__nav-item:hover {
    span::before {
      inline-size: 100%;
    }
  }
  &__email {
    display: none;
    font-size: 16px;
    font-weight: var(--fw-b);
    margin-block-end: 40px;
    color: var(--clr-dark);
  }
  &__socials {
    display: none;
  }
  &__btm {
    position: relative;
    display: grid;
    grid-template-areas: '. headbtminner .';
    grid-template-columns: 1fr min(1600px, 100%) 1fr;
  }
  &__btm-inner {
    grid-area: headbtminner;
    position: relative;
    display: flex;
    align-items: center;
    gap: 50px;
    gap: clamp(20px, calc(-29.92px + 5.00vw), 50px);
    margin-inline-start: 405px;
    margin-inline-start: clamp(370px, calc(311.68px + 5.83vw), 405px);
    padding: 20px var(--pad-right) 20px 0;
  }
  &__logo {
    position: absolute;
    inset-inline-start: -335px;
    inset-inline-start: clamp(-20.94rem, calc(-15.72rem + -4.35vw), -18.44rem);
    inset-block-start: -26px;
    inline-size: 204px;
    block-size: 171px;
  }
  &__town-select {
    position: relative;
  }
  &__town-select:hover {
    .header__town-options {
      visibility: visible;
      opacity: 1;
      transform: translateY(0px);
    }
  }
  &__town-option {
    display: flex;
    align-items: center;
    gap: 5px;
    color: var(--clr-dark);
    font-size: 14px;
    line-height: 1.15;
    text-align: left;
    span {
      border-block-end: 1px solid transparent;
      transition: border-color var(--tr);
    }
  }
  &__town-option:visited {
    color: var(--clr-dark);
  }
  &__town-option:hover {
    span {
      border-color: var(--clr-dark);
    }
  }
  &__town-option.current {
    font-size: 16px;
    font-weight: var(--fw-b);
    span {
      display: inline-block;
      border-color: var(--clr-dark);
    }
    i {
      font-size: 26px;
    }
  }
  &__town-options {
    position: absolute;
    inset-inline-start: 25px;
    inset-block-start: 100%;
    display: flex;
    flex-direction: column;
    gap: 6px;
    padding: 15px;
    background-color: var(--clr-bg);
    border-radius: 7px;
    outline: 1px solid var(--clr-dark);
    outline-offset: -5px;
    box-shadow: 0px 0px 20px 0px rgba(138, 133, 116, 0.38);
    visibility: hidden;
    opacity: 0;
    transform: translateY(-10px);
    transition: opacity var(--tr), transform var(--tr);
  }
  &__search {
    flex-grow: 1;
  }
  &__search-input {
    position: relative;
    display: flex;
    align-items: center;
    inline-size: 100%;
    block-size: 40px;
    padding-inline-start: 50px;
    border: 1px solid var(--clr-dark);
    border-radius: 100vw;
    background-image: url("data:image/svg+xml,%3Csvg version='1.1' id='Layer_1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' x='0px' y='0px' viewBox='0 0 20 20' style='enable-background:new 0 0 20 20;' xml:space='preserve'%3E%3Cstyle type='text/css'%3E .st0%7Bfill-rule:evenodd;clip-rule:evenodd;%7D%0A%3C/style%3E%3Cg%3E%3Cpath class='st0' d='M10.7,2.2c-3.9,0-7.1,3.2-7.1,7.1c0,1.8,0.6,3.4,1.7,4.6l-3,3c-0.2,0.2-0.2,0.6,0,0.8c0.2,0.2,0.6,0.2,0.8,0 l3-3c1.2,1.1,2.8,1.7,4.6,1.7c3.9,0,7.1-3.2,7.1-7.1S14.6,2.2,10.7,2.2z M10.7,15.3c-3.3,0-6-2.7-6-6c0-3.3,2.7-6,6-6 c3.3,0,6,2.7,6,6C16.7,12.6,14,15.3,10.7,15.3z'/%3E%3Cpath class='st0' d='M10.7,5.3c-1,0-1.9,0.4-2.6,1.1c-0.2,0.2-0.2,0.6,0,0.8c0.2,0.2,0.6,0.2,0.8,0c0.5-0.5,1.1-0.7,1.8-0.7 c0.7,0,1.3,0.3,1.8,0.7c0.2,0.2,0.6,0.2,0.8,0c0.2-0.2,0.2-0.6,0-0.8C12.6,5.7,11.7,5.3,10.7,5.3z'/%3E%3C/g%3E%3C/svg%3E");
    background-repeat: no-repeat;
    background-size: 24px;
    background-position: 16px center;
  }
  &__search-input::placeholder {
    font-size: 14px;
    color: var(--clr-sec);
  }
  &__search-input:focus-visible {
    outline: none;
    border-width: 2px;
  }
  &__search-submit {
    display: none;
  }
  &__phone {
    font-size: 20px;
    font-weight: var(--fw-b);
    color: var(--clr-dark);
    @media (max-width: 1240px) {
      display: none;
    }
  }
  &__misc {
    display: flex;
    gap: 30px;
    gap: clamp(15px, calc(-9.92px + 2.50vw), 30px);
    padding-inline-end: 60px;
  }
  &__misc-item {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    inline-size: 36px;
    block-size: 36px;
    background-color: transparent;
    border-radius: 100vw;
    transition: background-color var(--tr);
    i {
      color: var(--clr-dark);
      font-size: 24px;
      transition: color var(--tr);
    }
    .text {
      display: none;
    }
    .cart-num {
      position: absolute;
      inset-inline-start: 18px;
      inset-block-end: -6px;
      display: flex;
      padding: 1px 6px;
      color: var(--clr-white);
      background-color: var(--clr-dark-green);
      font-size: 14px;
      font-weight: var(--fw-b);
      line-height: 1;
      border-radius: 8px;
    }
    .cart-sum {
      position: absolute;
      display: block;
      inset-inline-start: 40px;
      inset-block-start: 11px;
      inline-size: 60px;
      color: var(--clr-dark);
      font-size: 14px;
      font-weight: var(--fw-b);
      transform: translateY(-3px);
    }
  }
  &__misc-item:hover {
    background-color: #A2BF70;
    i {
      color: var(--clr-white);
    }
  }
  &__misc-item.cat,
  &__misc-item.delivery {
    display: none;
  }
  &__burger {
    display: none;
    i {
      color: var(--clr-white);
      font-size: 36px;
    }
  }
  &__burger-item:last-child {
    i {
      font-size: 26px;
    }
  }
  &__burger-item.hidden {
    display: none;
  }
}

.header.header--fixed {
  position: fixed;
  z-index: 10;
  box-shadow: 0px 0px 10px 0px rgb(138 133 116 / 80%);
  transition: top .5s ease-in-out;
  .header__btm {
    inline-size: 100vw;
    background-color: var(--clr-light-green);
  }
  .header__btm-inner {
    padding: 8px var(--pad-right) 8px 0;
  }
  .header__logo {
    inset-block-start: 7px;
    inline-size: 172px;
    block-size: 41px;
  }
  .header__town-option.current {
    color: var(--clr-white);
    span {
      border-color: var(--clr-white);
    }
  }
  .header__search-input {
    block-size: 30px;
  }
  .header__phone {
    display: none;
  }
  .header__misc-item {
    i {
      color: var(--clr-white);
    }
    .cart-num {
      color: var(--clr-dark);
      background-color: var(--clr-bg-yellow);
    }
    .cart-sum {
      color: var(--clr-white);
    }
  }
  .header__burger {
    display: flex;
  }
}

.header.header--visible {
  top: 0px;
}

@media (max-width: 1000px) {
  .header {
    &__top {
      position: absolute;
      z-index: 1;
      display: flex;
      flex-direction: column;
      inline-size: 80vw;
      block-size: 100vh;
      padding: 0 15px 15px;
      background-color: var(--clr-white);
      // background-image: url('../img/misc/mob-menu-bg.webp');
      background-repeat: no-repeat;
      background-position: left bottom;
      background-size: 100%;
      transform: translateX(-80vw);
      transition: transform var(--tr);
    }
    &__mainlinks {
      display: flex;
      flex-direction: column;
      gap: 5px;
      margin-inline: -15px;
      margin-block-end: 25px;
    }
    &__mainlinks-item {
      display: flex;
      align-items: center;
      gap: 15px;
      padding-inline-start: 15px;
      padding-block: 15px;
      font-size: 15px;
      font-weight: var(--fw-b);
      color: var(--clr-dark);
      background-color: #ebebeb;
      i {
        color: var(--clr-dark);
        font-size: 32px;
      }
      i.hidden {
        display: none;
      }
      .icon-arrow {
        transform: rotate(180deg);
      }
    }
    &__mainlinks-item.catal.active {
      background-color: var(--clr-white);
    }
    &__nav {
      overflow-y: auto;
      overflow-x: hidden;
      flex-wrap: unset;
      justify-content: unset;
      align-items: unset;
      column-gap: unset;
      flex-direction: column;
      row-gap: 20px;
      // inline-size: max-content;
      inline-size: 200px;
      block-size: 100%;
      padding: 0 0 0 0;
      margin: unset;
      margin-block-end: 40px;
    }
    &__nav::-webkit-scrollbar {
      width: 5px;
    }
    &__nav::-webkit-scrollbar-track {
      background-color: transparent;
      border-radius: 100vw;
    }
    &__nav::-webkit-scrollbar-thumb {
      background-color: hsl(0 0% 0% / .3);
      border-radius: 100vw;
    }
    &__nav-item {
      display: inline;
      color: var(--clr-dark);
      font-size: 15px;
      font-weight: var(--fw-b);
      span::before {
        content: '';
        background-color: var(--clr-dark);
      }
    }
    &__email {
      display: block;
    }
    &__socials {
      display: flex;
      gap: 15px;
    }
    &__socials-item {
      display: flex;
      justify-content: center;
      align-items: center;
      inline-size: 30px;
      block-size: 30px;
      background-color: var(--clr-dark);
      border-radius: 50%;
      opacity: 1;
      transition: opacity var(--tr);
      i {
        color: var(--clr-white);
        font-size: 20px;
      }
    }
    &__socials-item:hover {
      opacity: 0.7;
    }
    /*------------------*/
    &__btm {
      background-color: var(--clr-light-green);
    }
    &__btm-inner {
      gap: 20px;
      block-size: 46px;
      margin-inline-start: unset;
      padding: 20px 16px 20px;
    }
    &__logo {
      inset-inline-start: 50%;
      inset-block-start: 10px;
      transform: translateX(-50%);
      inline-size: 88px;
      block-size: 74px;
    }
    &__town-select {
      order: 1;
      margin-inline-end: auto;
    }
    &__town-option.current {
      font-size: 13px;
      font-weight: var(--fw-r);
      color: var(--clr-white);
      span {
        border-color: var(--clr-white);
      }
      i {
        display: none;
      }
    }
    &__town-options {
      position: absolute;
      inset-inline-start: 0px;
      inset-block-start: 100%;
    }
    &__search {
      flex-grow: 1;
      display: none;
    }
    &__misc {
      position: fixed;
      inset-block-end: 0;
      justify-content: space-between;
      gap: 20px;
      inline-size: 100%;
      margin-inline: -16px;
      padding: 10px 30px 10px 16px;
      background-color: var(--clr-light-green);
    }
    &__misc-item {
      display: flex;
      flex-direction: column;
      align-items: center;
      i {
        color: var(--clr-white);
        font-size: 28px;
      }
      .text {
        display: block;
        font-size: 11px;
        line-height: 1;
        color: var(--clr-white);
      }
      .cart-num {
        inset-inline-start: 35px;
        inset-block-end: 15px;
        background-color: var(--clr-brown);
      }
      .cart-sum {
        display: none;
      }
    }
    &__misc-item:hover {
      background-color: transparent;
    }
    &__misc-item.cat {
      order: 0;
    }
    &__misc-item.delivery {
      order: 1;
    }
    &__misc-item.enter {
      order: 2;
    }
    &__misc-item.fav {
      order: 3;
    }
    &__misc-item.cart {
      order: 4;
    }
    &__misc-item.cat,
    &__misc-item.delivery {
      display: flex;
    }
    &__phone {
      order: 2;
      font-size: 13px;
      font-weight: var(--fw-r);
      color: var(--clr-white);
      display: block;
    }
    &__burger {
      order: 0;
      display: flex;
    }
  }
}

.header__top.active {
  transform: translateX(0vw);
}

.header__burger.active {
  position: absolute;
  z-index: 1;
  inset-inline-end: 10px;
  inset-block-start: 10px;
}

.header__menu-backdrop {
  position: absolute;
  z-index: 0;
  inset: 0;
  display: block;
  inline-size: 100vw;
  block-size: 100vh;
  background: rgba(0, 0, 0, 0.6);
}
