.uc-deliveries.other-deliveries {
  grid-template-areas: 'title title' 'slider subscript';
  grid-template-columns: 523px 1fr;
  margin-block-end: unset;
  h2 {
    grid-area: title;
  }
  .uc-deliveries__subscript {
    max-inline-size: 325px;
  }
}

@media (max-width: 1260px) {
  .uc-deliveries.other-deliveries {
    grid-template-columns: 340px 1fr;
    .uc-deliveries__subscript {
      gap: 15px;
      padding: 25px 10px 25px 72px;
    }
  }
}

@media (max-width: 1050px) {
  .uc-deliveries.other-deliveries {
    .uc-deliveries__subscript {
      padding: 25px 10px;
      background-image: none;
    }
  }
}

@media (max-width: 1000px) {
  .uc-deliveries.other-deliveries {
    grid-template-columns: 523px 1fr;
    h2 {
      justify-self: center;
    }
    .swiper-container {
      inline-size: unset;
    }
    .uc-deliveries__subscript {
      padding: 25px 10px 25px 72px;
      background-image: url('../img/svg/icons/icon-subscription.svg');
    }
  }
}

@media (max-width: 870px) {
  .uc-deliveries.other-deliveries {
    grid-template-columns: 330px 1fr;
  }
}

@media (max-width: 680px) {
  .uc-deliveries.other-deliveries {
    grid-template-columns: 1fr;
    column-gap: 0;
    .swiper-container {
      justify-self: center;
      inline-size: unset;
      max-inline-size: 330px;
    }
    .uc-deliveries__subscript {
      display: none;
    }
  }
}

@media (max-width: 480px) {
  .uc-deliveries.other-deliveries {
    row-gap: 20px;
    .swiper-container {
      max-inline-size: 300px;
    }
    a {
      inline-size: 145px;
      block-size: 145px;
      padding-inline: 16px;
    }
    time {
      font-size: 14px;
    }
    .name {
      font-size: 14px;
    }
    p {
      font-size: 11px;
    }
  }
}
