.air-datepicker {
  z-index: 1;
  inline-size: 310px;
  padding: 15px;
  font-family: var(--ff-prim);
  font-size: 15px;
  color: var(--clr-dark);
  border-color: var(--clr-dark-green);
  border-radius: 25px;
}

.air-datepicker .air-datepicker--pointer {
  display: none;
}

.air-datepicker-nav {
  border-bottom: none;
}

.air-datepicker-nav--action path {
  stroke: var(--clr-dark-green);
  // stroke-width: 2px;
}

.air-datepicker-nav--title {
  color: var(--clr-dark-green);
  font-family: var(--ff-prim);
  font-size: 18px;
  font-weight: var(--fw-b);
}

.air-datepicker-body--day-names {
  column-gap: 15px;
  margin: 8px 0 10px;
}

.air-datepicker-body--day-name {
  color: var(--clr-dark-green);
  font-size: 15px;
  text-transform: unset;
}

.air-datepicker-body--cells.-days- {
  -ms-grid-columns: (var(--adp-day-cell-width))[7];
  grid-template-columns: repeat(7, var(--adp-day-cell-width));
  grid-auto-rows: calc(var(--adp-day-cell-height) - 3px);
  column-gap: 15px;
  row-gap: 10px;
}

.air-datepicker-cell {
  border-radius: 100vw;
}

.air-datepicker-cell.-year-.-other-decade-,
.air-datepicker-cell.-day-.-other-month- {
  color: #bdbdbd;
}

.air-datepicker-cell.-selected-.-current- {
  color: #fff;
  background: var(--clr-dark-green);
}

.-selected-.air-datepicker-cell.-year-.-other-decade-,
.-selected-.air-datepicker-cell.-day-.-other-month- {
  background: var(--clr-dark-green);
}

.air-datepicker-cell.-selected- {
  background: var(--clr-dark-green);
}

.air-datepicker-cell.-selected-.-focus- {
  background: var(--clr-dark-green);
}

.air-datepicker-cell.-current- {
  color: var(--clr-dark);
}

.air-datepicker-buttons {
  display: flex;
  justify-content: end;
  gap: 5px;
  margin-block-start: 10px;
}

.air-datepicker--buttons,
.air-datepicker--time {
  border-top: none;
}

.air-datepicker-button {
  padding-inline: 20px;
  color: var(--clr-dark);
  font-size: 14px;
  font-weight: var(--fw-b);
  border-radius: 100vw;
  height: 30px;
  transition: background-color var(--tr);
}

.air-datepicker-button.custom-button {
  color: white;
  background-color: var(--clr-light-green);
}

.air-datepicker-button.custom-button:hover {
  color: white;
  background-color: var(--clr-dark-green);
}

@media (max-width: 500px) {
  .air-datepicker {
    inline-size: 290px;
    padding: 8px;
    font-size: 14px;
  }
}
