.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-block-end: 50px;
  color: var(--clr-dark);
  font-size: 18px;
  line-height: 1;
  &>* {
    display: flex;
    justify-content: center;
    align-items: center;
    aspect-ratio: 1/1;
    inline-size: 29px;
    block-size: auto;
    border-radius: 4px;
  }
  a,
  a:visited {
    transition: color var(--tr), background-color var(--tr);
    i {
      color: var(--clr-dark);
      transition: color var(--tr);
    }
  }
  a:hover,
  a:active {
    color: var(--clr-white);
    background-color: var(--clr-light-green);
  }
  .active {
    color: var(--clr-white);
    background-color: var(--clr-dark);
  }
  a.page-prev,
  a.page-next {
    background-color: var(--clr-white);
    transition: color var(--tr), background-color var(--tr);
  }
  a.page-prev:hover,
  a.page-prev:active,
  a.page-next:hover,
  a.page-next:active {
    color: var(--clr-white);
    background-color: var(--clr-light-green);
    i {
      color: var(--clr-white);
    }
  }
}
